import { useState } from 'react';
import { useDarkMode } from '../../../context/Dark-mode';


interface MenuComponentProps {
  setWhichContent: (content: string) => void;
}

const MenuComponent: React.FC<MenuComponentProps> = ({ setWhichContent }) => {

  const {darkMode} = useDarkMode()

  const [selectedCategory, setSelectedCategory] = useState<string | null>('LanguageDocs');

  const handleContentClick = (content: string) => {
    setWhichContent(content);
    setSelectedCategory(content)
  };

  return (
    <div
      className={`outBox absolute bg-transparent h-[60px] w-[100%] mt-[10px] translate-y-[-420px] ${darkMode ? 'text-white' : 'text-black'}`}
    >
      <div className='innerBox h-full w-full flex items-center justify-between rounded-lg'>
        <div className='menu1 w-full md:w-[100%] p-[5px] h-full rounded-xlg'>
          <button
            className={`w-full h-full rounded-lg  transition-all ${
              selectedCategory === 'LanguageDocs' ? `${darkMode ? 'bg-slate-500' : 'bg-[#c7c1bb]'} text-white` : ` ${darkMode ? 'bg-slate-400 hover:bg-slate-500' : 'hover:bg-[#c7c1bb] bg-[#e8e6e4]'}`
            } `}
            onClick={() => handleContentClick('LanguageDocs')}
          >
            <p
              className='text-md text-[11px] md:text-[17px] lg:text-[18px] font-bold mb-[10px]'
            >Language</p>
          </button>
        </div>
        <div className='menu1 w-full md:w-[100%] p-[5px] h-full rounded-xlg'>
          <button
            className={`w-full h-full rounded-lg  transition-all ${
              selectedCategory === 'FrontEndDocs' ? `${darkMode ? 'bg-slate-500' : 'bg-[#c7c1bb]'} text-white` : ` ${darkMode ? 'bg-slate-400 hover:bg-slate-500' : 'hover:bg-[#c7c1bb] bg-[#e8e6e4]'}`
            } `}
            onClick={() => handleContentClick('FrontEndDocs')}
          >
            <p
              className='text-md text-[11px] md:text-[17px] lg:text-[18px] font-bold mb-[10px]'
            >FrontEnd</p>
          </button>
        </div>
        <div className='menu1 w-full md:w-[100%] p-[5px] h-full rounded-xlg'>
          <button
            className={`w-full h-full rounded-lg  transition-all ${
              selectedCategory === 'BackEndDocs' ? `${darkMode ? 'bg-slate-500' : 'bg-[#c7c1bb]'}  text-white z-120` : ` ${darkMode ? 'bg-slate-400 hover:bg-slate-500' : 'hover:bg-[#c7c1bb] bg-[#e8e6e4]'}`
            } `}
            onClick={() => handleContentClick('BackEndDocs')}
          >
            <p
              className='text-md text-[11px] md:text-[17px] lg:text-[18px] font-bold mb-[10px]'
            >BackEnd</p>
          </button>
        </div>
        <div className='menu1 w-full md:w-[100%] p-[5px] h-full rounded-xlg'>
          <button
            className={`w-full h-full rounded-lg  transition-all ${
              selectedCategory === 'DataBaseDocs' ? `${darkMode ? 'bg-slate-500' : 'bg-[#c7c1bb]'} text-white` : ` ${darkMode ? 'bg-slate-400 hover:bg-slate-500' : 'hover:bg-[#c7c1bb] bg-[#e8e6e4]'}`
            } `}
            onClick={() => handleContentClick('DataBaseDocs')}
          >
            <p
              className='text-md text-[11px] md:text-[17px] lg:text-[18px] font-bold mb-[10px]'
            >DataBase</p>
          </button>
        </div>
        <div className='menu1 w-full md:w-[100%] p-[5px] h-full rounded-xlg'>
          <button
            className={`w-full h-full rounded-lg  transition-all ${
              selectedCategory === 'DevOpsDocs' ? `${darkMode ? 'bg-slate-500' : 'bg-[#c7c1bb]'} text-white` : ` ${darkMode ? 'bg-slate-400 hover:bg-slate-500' : 'hover:bg-[#c7c1bb] bg-[#e8e6e4]'}`
            } `}
            onClick={() => handleContentClick('DevOpsDocs')}
          >
            <p
              className='text-md text-[11px] md:text-[17px] lg:text-[18px] font-bold mb-[10px]'
            >DevOps</p>
          </button>
        </div>
      </div>
    </div>
  )
}

export default MenuComponent