
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent07 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/**
  데이터 정렬 : ORDER BY 
    형식 : SELECT [컬럼리스트]
      FROM [테이블명]
            WHERE [조건절]
            ORDER BY [컬럼명]  ASC / DESC
*/
-- 사원id 기준으로 오름차순 정렬 후 모든 데이터 출력
SELECT *
  FROM employee
    ORDER BY emp_id DESC;
    
-- 입사일이 가장 빠른 순서대로 사원들을 모두 조회
SELECT * FROM employee
ORDER BY hire_date;

-- 연봉이 가장 높은 순으로 사원들을 모두 조회
SELECT * FROM employee ORDER BY salary DESC;

-- eng_name이 NULL인 사원들 중 입사일이 가장 빠른순서대로 조회
SELECT * FROM employee WHERE eng_name IS NULL
ORDER BY hire_date;

-- 정보시스템 부서 사원 중 입사일이 가장 빠르고, 급여를 많이 받는
-- 순서로 정렬하여 조회
SELECT * FROM department;
SELECT * FROM employee 
  WHERE dept_id='SYS'
    ORDER BY hire_date ASC, salary DESC;
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent07