import { faX } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { useEffect, useState } from 'react';

interface siteNameProps{
  whichSite: string
  isSiteModalOpen: boolean;
  onClose: () => void;
}

const getWindowHeight = () =>  {
  return window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
}

const IframeComponentModal: React.FC<siteNameProps> = ({ whichSite, isSiteModalOpen, onClose }) => {
  
  const modalStyle = {
    backgroundColor: isSiteModalOpen ? "rgba(0, 0, 0, 0.8)" : "transparent",
    display: isSiteModalOpen ? "block" : "none",
    zIndex : 400,
  };
  const [, setWinHeight] = useState(getWindowHeight)

  const handleCloseClick = () => {
    onClose();
  };

  const handleBackdropClick = (e: React.MouseEvent<HTMLDivElement>) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  useEffect(() => {
  const handleResize = () => {
    setWinHeight(getWindowHeight());
  };

  window.addEventListener("resize", handleResize);

  return () => {
    window.removeEventListener("resize", handleResize);
  };
    
}, []);

  return (
    <div
      className={`modal fixed left-0 top-0 w-full h-[97.5%] lg:h-[98.5%] rounded-2xl bg-black opacity-100 transition-opacity duration-100`}
      style={modalStyle}
      onClick={handleBackdropClick}
    >
      <div
        className={`modal-content flex flex-col h-[786px] lg:h-[1160px] bg-transparent overflow-hidden rounded-lg opacity-100 mx-auto`}
        >  
        <section className="flex-1 p-[10px] flex flex-col items-center justify-center">
          <button
            className={`closeBtn absolute right-[15px] top-[15px] w-[20px] h-[25px] ml-auto mb-[10px] transition-all duration-500 bg-white text-black`}
            onClick={handleCloseClick}
            style={{
              textShadow: '4px 4px 4px rgba(255, 255, 255, 1)',
            }}
          >
            <FontAwesomeIcon icon={faX} />
          </button>
          <div className='iframeDiv w-[95%] h-[95%]'>
            <iframe
              title="내장 브라우저"
              className='rounded-lg'
              width="100%"
              height="100%"
              src={whichSite}
              frameBorder="0"
            />
          </div>
        </section>
      </div>
    </div>
  );

}
    

export default IframeComponentModal;



