import BackEndDocs from './contents/BackEndDocs/BackEndDocs';
import DataBaseDocs from './contents/DataBaseDocs/DataBaseDocs';
import FrontEndDocs from './contents/FrontEndDocs/FrontEndDocs';
import LanguageDocs from './contents/LanguageDocs/LanguageDocs';
import DevOpsDocs from './contents/DevOpsDocs/DevOpsDocs';



interface ContentProps {
  content: string;
}


const ContentComponent: React.FC<ContentProps> = ({ content }) => {
  let renderedContent = null;

  if (content === 'FrontEndDocs') {
    renderedContent = (
      <FrontEndDocs/>
    );
  } else if (content === 'BackEndDocs') {
    renderedContent = (
      <BackEndDocs/>
    );
  }  else if (content === 'LanguageDocs') {
    renderedContent = (
      <LanguageDocs/>
    );
  }  else if (content === 'DataBaseDocs') {
    renderedContent = (
      <DataBaseDocs/>
    );
  }   else if (content === 'DevOpsDocs') {
    renderedContent = (
      <DevOpsDocs/>
    );
  } 

  return renderedContent;
}

export default ContentComponent;