
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent12 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/**
	테이블 합치기 : UNION(DISTINCT), UNION ALL
    형식 : SELECT 쿼리
		  UNION / UNION ALL
          SELECT 쿼리
    - 쿼리 실행시에만 하나의 테이블로 출력됨 (물리적인 테이블 생성x)
    - 하나로 합쳐지는 컬럼명과 타입이 동일해야 함
*/
-- 영업부, 정보시스템 부서의 사원아이디, 사원명, 부서아이디, 입사일을 출력
SELECT emp_id, emp_name, dept_id, hire_date, CONCAT('id : ', dept_id) AS did FROM employee
WHERE dept_id = 'SYS'
UNION 
SELECT emp_id, emp_name, dept_id, hire_date, CONCAT('id : ', dept_id) AS did FROM employee
WHERE dept_id = 'MKT'
UNION
SELECT emp_id, emp_name, dept_id, hire_date, CONCAT('id : ', dept_id) AS did FROM employee
WHERE dept_id = 'SYS';

-- UNION ALL
SELECT emp_id, emp_name, dept_id, hire_date, CONCAT('id : ', dept_id) AS did FROM employee
WHERE dept_id = 'SYS'
UNION ALL
SELECT emp_id, emp_name, dept_id, hire_date, CONCAT('id : ', dept_id) AS did FROM employee
WHERE dept_id = 'MKT'
UNION ALL
SELECT emp_id, emp_name, dept_id, hire_date, CONCAT('id : ', dept_id) AS did FROM employee
WHERE dept_id = 'SYS';

-- 홍길동 사원이 속한 부서의 이름을 조회
SELECT * FROM department
WHERE dept_id = (SELECT dept_id FROM employee WHERE emp_name ='홍길동');

SELECT dept_id FROM employee WHERE emp_name ='홍길동';   
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent12