import { useState } from 'react';
import { useDarkMode } from '../../../../../context/Dark-mode';
import { AmazonAWSbutton, DockerComposebutton, DockerHubbutton, Dockerbutton, EC2button, GihubActionsbutton, GoogleCloudStoragebutton, Ubuntubutton } from '../../../../../icons/StackButtons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleDown, faAngleUp } from '@fortawesome/free-solid-svg-icons';
import DockerComponent from './docker/docker';
import DockerHubComponent from './dockerhub/dockerhub';
import DockerComposeComponent from './dockercompose/dockercompose';
import AmazonAWSComponent from './amazonaws/amazonaws';
import AmazonEC2Component from './amazonec2/amazonec2';
import UbuntuComponent from './ubuntu/ubuntu';
import GitHubActionsComponent from './githubactions/githubactions';
import GoogleCloudComponent from './googlecloud/googlecloud';


const DevOpsDocs = () => {

  const {darkMode} = useDarkMode()

  const [selectedCategory, setSelectedCategory] = useState<string | null>('docker');
  const [renderedContent, setRenderedContent] = useState<React.ReactNode | null>(<DockerComponent/>);
  const [isCategoryVisible, setIsCategroyVisible] = useState(true)

  const handleCategoryClick = (category: string) => {
    setSelectedCategory(category);
    setIsCategroyVisible(false)

    if (category === 'docker') {
      setRenderedContent(<DockerComponent />);
    } else if (category === 'dockerhub') {
      setRenderedContent(<DockerHubComponent />);
    } else if (category === 'dockercompose') {
      setRenderedContent(<DockerComposeComponent />);
    } else if (category === 'amazonaws') {
      setRenderedContent(<AmazonAWSComponent />);
    } else if (category === 'amazonec2') {
      setRenderedContent(<AmazonEC2Component />);
    } else if (category === 'ubuntu') {
      setRenderedContent(<UbuntuComponent />);
    } else if (category === 'githubactions') {
      setRenderedContent(<GitHubActionsComponent />);
    } else if (category === 'googlecloud') {
      setRenderedContent(<GoogleCloudComponent />);
    }
  };

  const clickDownBtn = () => {
    setIsCategroyVisible(true)
  }

  const clickUpBtn = () => {
    setIsCategroyVisible(false)
  }



  return (
    <div className='outBox w-full h-[95%] px-[5px] md:py-0 md:px-[20px]'>
      <div
        className={`w-[100%] relative h-[100%] flex flex-col items-center rounded-md text-center ${darkMode ? 'bg-slate-700' : 'bg-[#e8e6e4]'}`}>
        <section
          className={`fixed  flex flex-col justify-between w-[100%] px-[30px]  ${isCategoryVisible ? 'h-[95%] md:h-[200px]' : ''} ${darkMode ? 'bg-[#64748b]' : 'bg-[#c7c1bb]'} `}
          style={{ zIndex : 10 }}
        >
          <div className={`flex flex-col flex-wrap py-[20px] md:flex-row gap-2 items-center overflow-y-auto justify-center ${isCategoryVisible ? '' : 'hidden'}`}>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'docker' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'}` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('docker')}
            > 
              <Dockerbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px]  md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'dockerhub' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('dockerhub')}
            >
              <DockerHubbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'dockercompose' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('dockercompose')}
            >
              <DockerComposebutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'amazonaws' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('amazonaws')}
            >
              <AmazonAWSbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'amazonec2' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('amazonec2')}
            >
              <EC2button/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'ubuntu' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('ubuntu')}
            >
              <Ubuntubutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'githubactions' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('githubactions')}
            >
              <GihubActionsbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'googlecloud' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('googlecloud')}
            >
              <GoogleCloudStoragebutton/>
            </button>
          </div>
          <div className=''>
            <button
              className={`w-full ${isCategoryVisible ? '' : 'hidden'}`}
              onClick={clickUpBtn}
            >
                <FontAwesomeIcon icon={faAngleUp} />
              </button>
            <button
              className={`w-full flex flex-col items-center ${isCategoryVisible ? 'hidden' : ''}`}
              onClick={clickDownBtn}
            >
                <span className='my-[10px]'>Category</span>
                <FontAwesomeIcon icon={faAngleDown} />
              </button>
          </div>
        </section>
        <section className='w-full h-full mt-[60px]'>
          {renderedContent}
        </section>
      </div>
    </div>
  )
}

export default DevOpsDocs