import { useEffect, useState } from 'react';
import { faCircleXmark, faComments } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { useDarkMode } from '../context/Dark-mode';
import { EmailIcon, GitHubIcon, KakaoTalkIcon, LinkedInIcon } from '../icons/JustIcons';

export const BannerComponent = () => {
  const [isBannerVisible, setIsBannerVisible] = useState(false)
  const [isVisible, setIsVisible] = useState(false);
  const [windowHeight, setWindowHeight] = useState(window.innerHeight);
  const [componentHeight, setComponentHeight] = useState(windowHeight / 2 - 200);
  const [logoPos, setLogoPos] = useState({ x: 0, y: 0 });
  

  const { darkMode } = useDarkMode();

  const toggleVisibility = () => {
    if (window.scrollY > 100) {
      setIsBannerVisible(true)
    } else {
      setIsBannerVisible(false)
    }
  };

  const closeBtn = () => {
    setIsVisible(false);
    setLogoPos({  x: 10, y: window.innerHeight / 2 + 130 });
  };

  const openBtn = () => {
    setIsVisible(true);
    setLogoPos({ x: window.innerWidth - 80, y: 0 });
  }

  const sendEmail = () => {
    const email = 'jjdong9108@gmail.com';
    const subject = '제목을 입력하세요';
    const body = '내용을 입력하세요';

    const mailtoLink = `mailto:${email}?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;

    window.location.href = mailtoLink;
  };

  const clickKakaoBtn = () => {
    window.open('https://open.kakao.com/o/sjX0WRPf', '_blank');
  };
  const clickGitHubBtn = () => {
    window.open('https://github.com/Ju-jh', '_blank');
  };
  const clickLinkedInBtn = () => {
    window.open('https://www.linkedin.com/in/jayden-ju-984a23219/', '_blank');
  };


  const updateWindowDimensions = () => {
    setWindowHeight(window.innerHeight);
    setComponentHeight(window.innerHeight / 2 - 200);
  };



  useEffect(() => {
    setIsVisible(false)
    setLogoPos({  x: 10, y: window.innerHeight / 2 + 130 });
    window.addEventListener('scroll', toggleVisibility);
    window.addEventListener('resize', updateWindowDimensions);

    return () => {
      window.removeEventListener('scroll', toggleVisibility);
      window.removeEventListener('resize', updateWindowDimensions);
    };
  }, [windowHeight]);


  return (
    <div
      className={`fixed  top-0  rounded-md right-0 md:right-[10px] transition-all ${
          isVisible ? `flex-col items-center py-[30px] w-[80px] h-[400px] bg-white shadow-md shadow-slate-500 ${
        darkMode ? 'bg-slate-400' : 'bg-white'
        }` : ' w-[100px] h-[100px]'} ${isBannerVisible ? 'flex' : 'hidden'}`}
      style={{
        zIndex: 200,
        transform: `translateY(${componentHeight}px)`,
        top: logoPos.y,
        left: logoPos.x,
      }}
    >
      <button
        className={`w-[60px] h-[60px]  shadow-md   rounded-full flex flex-col items-center justify-center transition-all ${isVisible ? 'hidden opacity-0' : 'block opacity-100'} ${darkMode ? 'shadow-slate-500 bg-slate-200' : 'shadow-slate-400 bg-white'}`}
        onClick={openBtn}
      >
        <FontAwesomeIcon className='text-[30px]  ' icon={faComments} style={{color: `${darkMode ? '#0545b3' : '#86756b'}`}} />
      </button>
      <div className={`flex-col items-center justify-between w-full h-[95%] ${isVisible ? 'flex' : 'hidden'} `}>
        <div className={`flex-1 w-full h-full flex flex-col items-center justify-center `}>
          <button onClick={clickKakaoBtn}>
            <KakaoTalkIcon />
          </button>
        </div>
        <div className='flex-1 w-full h-full flex flex-col items-center justify-center'>
          <button onClick={sendEmail}>
            <EmailIcon />
          </button>
        </div>
        <div className='flex-1 w-full h-full flex flex-col items-center justify-center'>
          <button onClick={clickGitHubBtn}>
            <GitHubIcon />
          </button>
        </div>
        <div className='flex-1 w-full h-full flex flex-col items-center justify-center'>
          <button onClick={clickLinkedInBtn}>
            <LinkedInIcon />
          </button>
        </div>
      </div>
      <button onClick={closeBtn} className={`hover:text-blue-500 translate-y-[20px] text-[20px] mx-auto w-full ${isVisible ? 'block' : 'hidden'}`}>
        <FontAwesomeIcon icon={faCircleXmark} />
      </button>
    </div>
  );
};
