import React, { createContext, useContext, useState, ReactNode } from 'react';
import { components } from '../component/projects/backend/Backend'; //

const BackendContext = createContext<{ currentIndex: number; handleSwipeLeft: () => void; handleSwipeRight: () => void } | undefined>(undefined);

interface BackendProviderProps {
  children: ReactNode;
}

export const BackendProvider: React.FC<BackendProviderProps> = ({ children }) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const handleSwipeLeft = () => {
    setCurrentIndex((prevIndex) => (prevIndex - 1 + components.length) % components.length);
  };

  const handleSwipeRight = () => {
    setCurrentIndex((prevIndex) => (prevIndex + 1) % components.length);
  };

  return (
    <BackendContext.Provider value={{ currentIndex, handleSwipeLeft, handleSwipeRight }}>
      {children}
    </BackendContext.Provider>
  );
};

export const useBackendContext = () => {
  const context = useContext(BackendContext);
  if (context === undefined) {
    throw new Error('useBackendContext must be used within a BackendProvider');
  }
  return context;
};
