import { useEffect, useState } from 'react';
import { useDarkMode } from '../../../../../../context/Dark-mode';
import { faAnglesLeft, faBars } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';



const HtmlComponent = () => {

  const {darkMode} = useDarkMode()
  const [content, setContent] = useState<string | null>(null);
  const [activeButton, setActiveButton] = useState<number>(1);

  const [isMenuActive, setIsMenuActive] = useState<boolean>(true)
  
  const openMenuBtn = () => {
    setIsMenuActive(true)
  }

    const closeMenuBtn = () => {
    setIsMenuActive(false)
    }
  
  const loadHtmlFile = async (fileName: string, buttonIndex: number) => {
    try {
      const response = await fetch(`frontend/html/${fileName}`);
      const htmlContent = await response.text();
      setContent(htmlContent);
      setActiveButton(buttonIndex)
    } catch (error) {
      console.error('Error loading HTML file:', error);
    }
  };

  useEffect(() => {
    loadHtmlFile('01 html문서의 개념/index.html',1);
  }, []); 



  return (
    <div className='outBox w-full h-[100%] px-[5px] md:py-0 md:px-[20px]'>
      <div className={`w-[100%] h-[90%] lg:h-[93%] text-center ${darkMode ? 'bg-slate-700' : 'bg-[#e8e6e4]'} `}>
        <section className='w-full  bg-white h-[100%]  flex items-start justify-start'>
          <div className={`bg-white h-[100%] border-r-2 border-slate-300 font-bold transition-all ${isMenuActive ? ' min-w-[280px]':' w-[0px]'}`}>
            <div className={`flex flex-col items-start  h-full p-[30px] md:py-[50px] text-[13px] md:text-[15px]  ${darkMode ? 'bg-slate-400 text-slate-900' : 'bg-[#86756b] text-white'} `}>
              <div className='w-full flex items-center px-[10px] mb-[30px]'>
                <span className={` w-full text-start text-[20px] ${isMenuActive ? 'block' : 'hidden'}`}>목차</span>
              {isMenuActive && <button
                    onClick={closeMenuBtn}
                    className='hover:bg-slate-200 rounded-md'
                  >
                    <FontAwesomeIcon icon={faAnglesLeft} className='text-[18px]' />
                  </button>}
              </div>
              <ol className={`flex flex-col ml-[10px] md:ml-[20px] lg:ml-[30px] overflow-y-auto  items-start ${isMenuActive ? 'block' : 'hidden'}`}>
                <li>
                  <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start sm:text-[13px] transition-all  ${activeButton === 1 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('01 html문서의 개념/index.html',1)}>01 html문서의 개념</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start sm:text-[13px] transition-all ${activeButton === 2 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('02 anchor/index.html',2)}>02 anchor</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start sm:text-[13px] transition-all ${activeButton === 3 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('03 img출력/index.html',3)}>03 img출력</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start sm:text-[13px] transition-all ${activeButton === 4 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('04 리스트/index.html',4)}>04 리스트</button>
                </li>
                <li>
                  <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start sm:text-[13px] transition-all ${activeButton === 5 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('05 프레임/index.html',5)}>05 프레임</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start sm:text-[13px] transition-all ${activeButton === 6 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('06 form 요소/index.html',6)}>06 form 요소</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start sm:text-[13px] transition-all ${activeButton === 7 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('07 table/index.html',7)}>07 table</button>
                </li>
                <li>
              <button className={`hover:text-blue-500 hover:underline text-start sm:text-[13px] transition-all ${activeButton === 8 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('08 문법유효성/index.html',8)}>08 문법 유효성</button>
                </li>
              </ol>
            </div>
          </div>
          <div className='absolute p-[20px] pr-0'>
            {!isMenuActive && <button
              onClick={openMenuBtn}
              className=' '
            >
              <FontAwesomeIcon icon={faBars} className={`text-[24px] ${darkMode ? 'text-white' : 'text-black'}`} />
            </button>}
          </div>
          <div className={`w-full h-full pt-[60px] p-[20px] md:p-[50px] overflow-y-auto text-start ${darkMode ? 'bg-slate-900 text-slate-100' : 'bg-white text-black'}`} dangerouslySetInnerHTML={{ __html: content || '' }} />
        </section>
      </div>
    </div>
  )
}

export default HtmlComponent