import { useState } from 'react';
import { useDarkMode } from '../../../../../context/Dark-mode';
import NestComponent from './nest/nest';
import FastifyComponent from './fastify/fastify';
import { ExpressJsbutton, Fastifybutton, Flaskbutton, NestJsbutton, SpringBootbutton, Springbutton } from '../../../../../icons/StackButtons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleDown, faAngleUp } from '@fortawesome/free-solid-svg-icons';
import ExpressComponent from './express/express';
import FlaskComponent from './flask/flask';
import SpringComponent from './spring/spring';
import SpringBootComponent from './springboot/springboot';




const BackEndDocs = () => {

  const {darkMode} = useDarkMode()

  const [selectedCategory, setSelectedCategory] = useState<string | null>('nest');
  const [renderedContent, setRenderedContent] = useState<React.ReactNode | null>(<NestComponent/>);
  const [isCategoryVisible, setIsCategroyVisible] = useState(true)

  const handleCategoryClick = (category: string) => {
    setSelectedCategory(category);
    setIsCategroyVisible(false)

    if (category === 'nest') {
      setRenderedContent(<NestComponent />);
    } else if (category === 'fastify') {
      setRenderedContent(<FastifyComponent />);
    } else if (category === 'express') {
      setRenderedContent(<ExpressComponent />);
    } else if (category === 'flask') {
      setRenderedContent(<FlaskComponent />);
    } else if (category === 'spring') {
      setRenderedContent(<SpringComponent />);
    } else if (category === 'springboot') {
      setRenderedContent(<SpringBootComponent />);
    }
  };

  const clickDownBtn = () => {
    setIsCategroyVisible(true)
  }

  const clickUpBtn = () => {
    setIsCategroyVisible(false)
  }



  return (
    <div className='outBox w-full h-[95%] px-[5px] md:py-0 md:px-[20px]'>
      <div
        className={`w-[100%] relative h-[100%] flex flex-col items-center rounded-md text-center ${darkMode ? 'bg-slate-700' : 'bg-[#e8e6e4]'}`}>
        <section
          className={`fixed  flex flex-col justify-between w-[100%] px-[30px]  ${isCategoryVisible ? 'h-[95%] md:h-[200px]' : ''} ${darkMode ? 'bg-[#64748b]' : 'bg-[#c7c1bb]'} `}
          style={{ zIndex : 10 }}
        >
          <div className={`flex flex-col flex-wrap py-[20px] md:flex-row gap-2 items-center overflow-y-auto justify-center ${isCategoryVisible ? '' : 'hidden'}`}>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'nest' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'}` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('nest')}
            > 
              <NestJsbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px]  md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'fastify' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('fastify')}
            >
              <Fastifybutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'express' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('express')}
            >
              <ExpressJsbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'flask' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('flask')}
            >
              <Flaskbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'spring' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('spring')}
            >
              <Springbutton/>
            </button>
            <button
              className={`relative p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'springboot' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('springboot')}
            >
              <div className="absolute bg-gray-800 text-white p-2 rounded-md mt-2 opacity-0 invisible group-hover:opacity-100 group-hover:visible ">
                Spring Boot
              </div>
              <SpringBootbutton/>
            </button>
          </div>
          <div className=''>
            <button
              className={`w-full ${isCategoryVisible ? '' : 'hidden'}`}
              onClick={clickUpBtn}
            >
                <FontAwesomeIcon icon={faAngleUp} />
              </button>
            <button
              className={`w-full flex flex-col items-center ${isCategoryVisible ? 'hidden' : ''}`}
              onClick={clickDownBtn}
            >
                <span className='my-[10px]'>Category</span>
                <FontAwesomeIcon icon={faAngleDown} />
              </button>
          </div>
        </section>
        <section className='w-full h-full mt-[60px]'>
          {renderedContent}
        </section>
      </div>
    </div>
  )
}

export default BackEndDocs