
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent08 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/**
  특정데이터를 검색 : WHERE 조건절 + 비교연산자
    형식 : SELECT [컬럼리스트] FROM [테이블명] WHERE [조건절 + 비교연산자]  
*/
-- 연봉이 5000 이상인 사원들만 연봉이 높은순으로 조회
SELECT * FROM employee WHERE salary >= 5000 ORDER BY salary DESC;

-- 2015년 1월 1일 이후 입사자들을 입사일이 빠른 순으로 조회
-- 조회기준 연봉이 NULL인 직원의 값은 0으로 정의
SELECT emp_id, emp_name, gender, hire_date, IFNULL(salary, 0) AS salary
FROM employee WHERE hire_date >= '2015-01-01'
ORDER BY hire_date DESC;

-- 입사일이 2016년 1월 1일 이후 이고, 연봉이 6000 이상인 사원들을 조회
-- 최근 입사일 기준으로 정렬
SELECT * FROM employee
WHERE hire_date >= '2016-01-01' AND salary >= 6000
ORDER BY hire_date DESC;

-- 입사일이 2016년 1월 1일 이후 이거나, 연봉이 6000 이상인 사원들을 조회
-- 최근 입사일 기준으로 정렬
SELECT * FROM employee
WHERE hire_date >= '2016-01-01' OR salary >= 6000
ORDER BY hire_date DESC;

-- 정보시스템 부서 중 급여가 7000 이상인 사원들만 조회
SELECT * FROM department;
SELECT * FROM employee 
WHERE dept_id='SYS' AND salary >= 7000;

-- 영업부 직원들의 사원아이디, 사원명, 성별, 입사일, 이메일
-- 연봉, 보너스(연봉의 20%) 조회
-- 연봉이 NULL인 사원은 기본값을 50으로 정의
-- 보너스가 1000 이상인 사원 조회
SELECT emp_id, emp_name, gender, hire_date, email, salary
    , IFNULL(salary*0.2, 50) AS bonus
FROM employee
WHERE dept_id = 'MKT' AND salary*0.2 >= 1000;

-- 범위 조회 - BETWEEN ~ AND
-- 형식 : WHERE [컬럼명] BETWEEN [시작] AND [종료] - [시작] 이상 [종료] 이하
-- 연봉이 6000 이상, 7000 미만인 사원들만 조회
SELECT * FROM employee
WHERE salary >= 6000 AND salary < 7000;

SELECT * FROM employee
WHERE salary BETWEEN 6000 AND 6999;

-- 입사년도가 2015년(2015-01-01 ~ 2015-12-31)인 사원들만 조회
SELECT * FROM employee
WHERE hire_date >= '2015-01-01' AND hire_date <= '2015-12-31';

SELECT * FROM employee
WHERE hire_date BETWEEN '2015-01-01' AND '2015-12-31';

-- 정보시스템 부서와 영업부서의 사원들만 조회
-- OR 논리연산자 : IN (값1, 값2...)
-- 형식 : WHERE [컬럼명] IN (값1, 값2...)  
SELECT * FROM employee
WHERE dept_id = 'SYS' OR dept_id = 'MKT';

SELECT * FROM employee
WHERE dept_id IN ('SYS', 'MKT');
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent08