import FrontendBasic from './frontendProjects/FrontendHowToUse';
import FrontendMovie from './frontendProjects/FrontendMovie';
import FrontendWebbler from './frontendProjects/FrontendWebbler';
import { useDarkMode } from '../../../context/Dark-mode';
import { useFrontendContext } from '../../../context/FrontendContext';
import FrontendCoinView from './frontendProjects/FrontendCoinView';
import FrontendMyBlog from './frontendProjects/FrontendMyBlog';
import { useEffect, useState } from 'react';
import FrontendCookify from './frontendProjects/FrontendCookify';

export const components = [
  <FrontendCookify />,
  <FrontendCoinView />,
  <FrontendMyBlog/>,
  <FrontendMovie />,
  <FrontendWebbler />,
  <FrontendBasic />,
];

const FrontendContainer = () => {
  const {darkMode} = useDarkMode()
  const [parentWidth, setParentWidth] = useState(0);

  const { currentIndex } = useFrontendContext();

  useEffect(() => {
    const handleResize = () => {
      const parentElement = document.getElementById('parent');
      if (parentElement) {
        setParentWidth(parentElement.offsetWidth); 
      }
    };
    handleResize();

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const width = parentWidth / components.length; 

  return (
    <div className='w-full h-[600px] lg:h-[1000px] my-auto p-[5px] pt-0 md:p-[20px] md:pt-0'>
      <div id="parent" className='w-[100%] my-[1px] h-[15px] md:h-[18px] rounded-md bg-slate-300  top-0'>
        <div style={{ width: `${width}px`, transform : `translateX(${width * currentIndex - 1}px)` }} className={`flex flex-col items-center justify-center h-[100%] transition-all  rounded-md border-[2px] ${darkMode ? 'bg-blue-500 border-slate-300' : 'bg-[#86756b] border-slate-300'} `}>
          <span className={`font-bold text-[15px] ${darkMode ? 'text-white' : 'text-white'}`}>{currentIndex + 1}</span>
        </div>
      </div>
      <div className={` h-full flex flex-col items-center justify-center relative ${darkMode ? 'bg-slate-900 text-white' : 'bg-white text-black'}`}>
        {components[currentIndex]}
      </div>
    </div>
  );
};

export default FrontendContainer;