import React, { createContext, useContext, useState, ReactNode } from 'react';
import { components } from '../component/projects/frontend/Frontend';

const FrontendContext = createContext<{ currentIndex: number; handleSwipeLeft: () => void; handleSwipeRight: () => void } | undefined>(undefined);

interface FrontendProviderProps {
  children: ReactNode;
}

export const FrontendProvider: React.FC<FrontendProviderProps> = ({ children }) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const handleSwipeLeft = () => {
    setCurrentIndex((prevIndex) => (prevIndex - 1 + components.length) % components.length);
  };

  const handleSwipeRight = () => {
    setCurrentIndex((prevIndex) => (prevIndex + 1) % components.length);
  };

  return (
    <FrontendContext.Provider value={{ currentIndex, handleSwipeLeft, handleSwipeRight }}>
      {children}
    </FrontendContext.Provider>
  );
};

export const useFrontendContext = () => {
  const context = useContext(FrontendContext);
  if (context === undefined) {
    throw new Error('useFrontendContext must be used within a FrontendProvider');
  }
  return context;
};
