import { ScrollToTop } from '../../api/ScrollToTop-api';
import { useDarkMode } from '../../context/Dark-mode';


const Footer = () => {

  const { darkMode } = useDarkMode();

  return (
    <footer
      className={`footer w-full h-[100px] md:h-[200px] min-w-[350px] text-[13px] md:text-[15px]  ${darkMode ? 'bg-slate-900' : 'bg-[#86756b]'}`}
      style={{fontFamily: 'marcellus', zIndex:10 }}
    >
      <div className=' text-white w-full h-full flex flex-col items-center justify-center'>
        <p>© 2023 @JU_JH ALL RIGHTS RESERVED</p>
      </div>
      <ScrollToTop />
    </footer>
  );
};

export default Footer;