import { useCallback, useEffect, useState } from 'react';
import { useDarkMode } from '../../../../../../context/Dark-mode';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAnglesLeft, faBars } from '@fortawesome/free-solid-svg-icons';


const Es6Component = () => {

  const { darkMode } = useDarkMode();
  const [content, setContent] = useState<string | null>(null);
  const [activeButton, setActiveButton] = useState<number>(1);

  const [isMenuActive, setIsMenuActive] = useState<boolean>(true)
  
  const openMenuBtn = () => {
    setIsMenuActive(true)
  }

    const closeMenuBtn = () => {
    setIsMenuActive(false)
    }

  let scriptElement: HTMLScriptElement | null = null;

  const loadHtmlFile = useCallback(async (fileName: string, buttonIndex: number) => {
    try {
      if (scriptElement) {
        document.body.removeChild(scriptElement);
      }

      const response = await fetch(`frontend/es6/${fileName}/index.html`);
      const htmlContent = await response.text();
      setContent(htmlContent);
      setActiveButton(buttonIndex);

      const scriptResponse = await fetch(`frontend/es6/${fileName}/js/script.js`);
      const scriptCode = await scriptResponse.text();

      // eslint-disable-next-line react-hooks/exhaustive-deps
      scriptElement = document.createElement('script');
      scriptElement.innerHTML = scriptCode;
      document.body.appendChild(scriptElement);
    } catch (error) {
      console.error('Error loading HTML file:', error);
    }
  },[]);

  useEffect(() => {
    loadHtmlFile('01 스크립트호출 호이스팅 스코프',1);
  }, [loadHtmlFile]);


  return (
    <div className='outBox w-full h-[100%] px-[5px] md:py-0 md:px-[20px]'>
      <div className={`w-[100%] h-[90%] lg:h-[93%] text-center ${darkMode ? 'bg-slate-700' : 'bg-[#e8e6e4]'} `}>
        <section className='w-full  bg-white h-[100%]  flex items-start justify-start'>
          <div className={`bg-white h-[100%] border-r-2 border-slate-300 font-bold transition-all ${isMenuActive ? ' min-w-[280px]':' w-[0px]'}`}>
            <div className={`flex flex-col items-start  h-full p-[30px] md:py-[50px] text-[13px] md:text-[15px]  ${darkMode ? 'bg-slate-400 text-slate-900' : 'bg-[#86756b] text-white'} `}>
              <div className='w-full flex items-center px-[10px] mb-[30px]'>
              <span className={` w-full text-start text-[20px] ${isMenuActive ? 'block' : 'hidden'}`}>목차</span>
              {isMenuActive && <button
                    onClick={closeMenuBtn}
                    className='hover:bg-slate-200 rounded-md'
                  >
                    <FontAwesomeIcon icon={faAnglesLeft} className='text-[18px]' />
                  </button>}
              </div>
              <ol className={`flex flex-col ml-[10px] md:ml-[20px] lg:ml-[30px] overflow-y-auto text-[13px] items-start ${isMenuActive ? 'block' : 'hidden'}`}>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all  ${activeButton === 1 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('01 스크립트호출 호이스팅 스코프',1)}>01 스크립트호출 호이스팅 <p>스코프</p></button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all  ${activeButton === 2 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('02 배열기초',2)}>02 배열기초</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all  ${activeButton === 3 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('03 for 반복문',3)}>03 for 반복문</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all  ${activeButton === 4 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('04 array',4)}>04 array</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all  ${activeButton === 5 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('05 object',5)}>05 object</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all  ${activeButton === 6 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('06 객체 키설정과 구조분해',6)}>06 객체 키설정과 구조분해</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all  ${activeButton === 7 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('07 shallowCopy deepCopy 전개연산자 map함수',7)}>07 shallowCopy deepCopy <p>전개연산자 map함수</p></button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all  ${activeButton === 8 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('08 filter sort',8)}>08 filter sort</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all  ${activeButton === 9 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('09 local storage',9)}>09 local storage</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all  ${activeButton === 10 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('10 비동기',10)}>10 비동기</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all  ${activeButton === 11 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('11 비동기2',11)}>11 비동기2</button>
                </li>
                <li>
              <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all  ${activeButton === 12 ? `${darkMode ? 'text-blue-600' : 'text-blue-400' }` : ''}`} onClick={() => loadHtmlFile('12 fetch',12)}>12 fetch</button>
                </li>
              </ol>
            </div>
          </div>
          <div className='absolute p-[20px] pr-0'>
            {!isMenuActive && <button
              onClick={openMenuBtn}
              className=' '
            >
              <FontAwesomeIcon icon={faBars} className={`text-[24px] ${darkMode ? 'text-white' : 'text-black'}`} />
            </button>}
          </div>
          <div className={`w-full h-full pt-[60px] p-[20px] md:p-[50px] overflow-y-auto text-start ${darkMode ? 'bg-slate-900 text-slate-100' : 'bg-white text-black'}`} dangerouslySetInnerHTML={{ __html: content || '' }} />
        </section>
      </div>
    </div>
  )
}

export default Es6Component