import { faMoon, faSun } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { useDarkMode } from '../../context/Dark-mode';
import { useEffect, useState } from 'react';

const Header = () => {

  const { darkMode, toggleDarkMode } = useDarkMode();
  const [isHeaderVisible, setHeaderVisible] = useState(true);
  const [prevScrollPosition, setPrevScrollPosition] = useState(0);
  const [imageLoaded, setImageLoaded] = useState(false);

  const handleScroll = () => {
    const scrollPosition = window.scrollY;
    if (scrollPosition > 25 && scrollPosition > prevScrollPosition) {
      setHeaderVisible(false);
    } else {
      setHeaderVisible(true);
    }
    setPrevScrollPosition(scrollPosition);
  };

  const loadImage = (src: string) => {
  return new Promise((resolve, reject) => {
    const img = new Image();
    img.onload = () => {
      resolve(img);
    };
    img.onerror = (error) => {
      console.error('Error loading image:', error);
      reject(error);
    };
    img.src = src;
  });
};

  const imageSrc = "https://drive.google.com/uc?export=view&id=1sCSLY63IPsT2sxzqBLX_1ayOurgflyml";
    loadImage(imageSrc)
      .then(() => {
        setImageLoaded(true);
      })
      .catch((error) => {
        console.error('Error loading image:', error);
      });


  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [prevScrollPosition]);

  return (<header
      className={`backGroundBox min-w-[350px] w-full h-[60px]  transition-transform duration-300 ${ darkMode ? 'bg-slate-900 shadow-slate-700' : 'bg-[#86756b] shadow-[#917e73]' } ${ isHeaderVisible  ? 'shadow-md' :'translate-y-[-60px]'}`}
      style={{ position: 'fixed', zIndex: 100, fontFamily: 'marcellus' }}
      >
      <section
        className='InnerGroundBoxTitle flex items-center justify-center '
        style={{
                  zIndex: 20
                }}
      >
        <a href="/" className=' block pt-[14px] md:pt-[10px] '>
          <span
          className={`text-white text-[20px] md:text-[26px] transition-all`}
          
            style={{
              textShadow: `2px 2px 4px rgba(0, 0, 0, 0.5)`,
              zIndex: 20
            }}
          >
            Jaehun Ju's Blog</span>
        </a>
      </section>
      <section
        className='absolute w-full opacity-90 flex translate-y-[-47px] md:translate-y-[-50px] lg:translate-y-[-50px]'
        style={{ zIndex : -30 }}
      >
        {imageLoaded && <img className='hidden lg:block h-[40px] w-[100%]' src='/christmasheader2.png' alt="" />}
      </section>
      <section className='w-full ml-[95%] translate-x-[-3%] md:translate-x-[1%] translate-y-[-25px] md:translate-y-[-30px]'>
        <button
          onClick={toggleDarkMode}
          className={`focus:outline-none transition-opacity ${ darkMode ? 'text-white' : 'text-black' } `}
          style={{
            textShadow: `${darkMode ? '2px 2px 4px rgba(0, 0, 0, 0.5)' : '2px 2px 4px rgba(255, 255, 255, 1)'}`,
          }}
        >
          {!darkMode && (
            <FontAwesomeIcon
              icon={faMoon}
              className='text-xl md:text-2xl text-yellow-300'
              style={{
                textShadow: '10px 10px 10px rgba(0, 0, 0, 1)',
              }}
            /> 
          )}
          {darkMode && (
            <FontAwesomeIcon
              icon={faSun}
              className='text-xl md:text-2xl'
            />
          )}
        </button>
      </section>
    </header>)
  
}

export default Header