import { useState } from 'react';
import { useDarkMode } from '../../../../../context/Dark-mode';
import CssComponent from './css/css';
import Es6Component from './es6/es6';
import HtmlComponent from './html/html';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleDown, faAngleUp } from '@fortawesome/free-solid-svg-icons';
import { CSSbutton, ES6button, HTMLbutton, Nextbutton, Phpbutton, PostCSSbutton, Reactbutton, SCSSbutton, TailwindCSSbutton, Vitebutton } from '../../../../../icons/StackButtons';
import PostCSSComponent from './postcss/postcss';
import SCSSComponent from './scss/scss';
import TailwindCSSComponent from './tailwindcss/tailwindcss';
import PHPComponent from './php/php';
import ReactComponent from './react/react';
import NextComponent from './next/next';
import ViteComponent from './vite/vite';



const FrontEndDocs = () => {

  const {darkMode} = useDarkMode()

  const [selectedCategory, setSelectedCategory] = useState<string | null>('html');
  const [renderedContent, setRenderedContent] = useState<React.ReactNode | null>(<HtmlComponent/>);
  const [isCategoryVisible, setIsCategroyVisible] = useState(true)


  const handleCategoryClick = (category: string) => {
    setSelectedCategory(category);
    setIsCategroyVisible(false)

    if (category === 'html') {
      setRenderedContent(<HtmlComponent />);
    } else if (category === 'css') {
      setRenderedContent(<CssComponent />);
    } else if (category === 'es6') {
      setRenderedContent(<Es6Component />);
    } else if (category === 'postcss') {
      setRenderedContent(<PostCSSComponent />);
    } else if (category === 'scss') {
      setRenderedContent(<SCSSComponent />);
    } else if (category === 'tailwindcss') {
      setRenderedContent(<TailwindCSSComponent />);
    } else if (category === 'php') {
      setRenderedContent(<PHPComponent />);
    } else if (category === 'react') {
      setRenderedContent(<ReactComponent />);
    } else if (category === 'next') {
      setRenderedContent(<NextComponent />);
    } else if (category === 'vite') {
      setRenderedContent(<ViteComponent />);
    }
  };

  const clickDownBtn = () => {
    setIsCategroyVisible(true)
  }

  const clickUpBtn = () => {
    setIsCategroyVisible(false)
  }


  return (
    <div className='outBox w-full h-[95%] px-[5px] md:py-0 md:px-[20px]'>
      <div
        className={`w-[100%] relative h-[100%] flex flex-col items-center rounded-md text-center ${darkMode ? 'bg-slate-700' : 'bg-[#e8e6e4]'}`}>
        <section
          className={`fixed  flex flex-col justify-between w-[100%] px-[30px]  ${isCategoryVisible ? 'h-[95%] md:h-[200px]' : ''} ${darkMode ? 'bg-[#64748b]' : 'bg-[#c7c1bb]'} `}
          style={{ zIndex : 10 }}
        >
          <div className={`flex flex-col flex-wrap py-[20px] md:flex-row gap-2 items-center overflow-y-auto justify-center ${isCategoryVisible ? '' : 'hidden'}`}>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
              selectedCategory === 'html' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'}` : ` ${darkMode ? 'text-white ' : 'text-black '} `
            }`}
            onClick={() => handleCategoryClick('html')}
            >
              <HTMLbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'css' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('css')}
            >
              <CSSbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'es6' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('es6')}
            >
              <ES6button/>
            </button>
            {/* <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'jquery' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('jquery')}
            >
              <JQuerybutton/>
            </button> */}
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'postcss' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('postcss')}
            >
              <PostCSSbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'scss' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('scss')}
            >
              <SCSSbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'tailwindcss' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('tailwindcss')}
            >
              <TailwindCSSbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'php' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('php')}
            >
              <Phpbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'react' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('react')}
            >
              <Reactbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'next' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('next')}
            >
              <Nextbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'vite' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('vite')}
            >
              <Vitebutton/>
            </button>

          </div>
          <div className=''>
            <button
              className={`w-full ${isCategoryVisible ? '' : 'hidden'}`}
              onClick={clickUpBtn}
            >
                <FontAwesomeIcon icon={faAngleUp} />
              </button>
            <button
              className={`w-full flex flex-col items-center ${isCategoryVisible ? 'hidden' : ''}`}
              onClick={clickDownBtn}
            >
                <span className='my-[10px]'>Category</span>
                <FontAwesomeIcon icon={faAngleDown} />
              </button>
          </div>
        </section>
        <section className='w-full h-full mt-[60px]'>
        {renderedContent}
        </section>
      </div>
    </div>
  )
}

export default FrontEndDocs