import { useState } from 'react';
import { useDarkMode } from '../../../../../context/Dark-mode';
import { JavaScriptbutton, Javabutton, Pythonbutton, TypeScriptbutton } from '../../../../../icons/StackButtons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleDown, faAngleUp } from '@fortawesome/free-solid-svg-icons';
import JavaScriptComponent from './javascript/javascript';
import TypeScriptComoponent from './typescript/typescript';
import JavaComponent from './java/java';
import PythonComponent from './python/python';




const LanguageDocs = () => {

  const {darkMode} = useDarkMode()

  const [selectedCategory, setSelectedCategory] = useState<string | null>('javascript');
  const [renderedContent, setRenderedContent] = useState<React.ReactNode | null>(<JavaScriptComponent/>);
  const [isCategoryVisible, setIsCategroyVisible] = useState(true)

  const handleCategoryClick = (category: string) => {
    setSelectedCategory(category);
    setIsCategroyVisible(false)
    if (category === 'javascript') {
      setRenderedContent(<JavaScriptComponent />);
    } else if (category === 'typescript') {
      setRenderedContent(<TypeScriptComoponent />);
    } else if (category === 'python') {
      setRenderedContent(<PythonComponent />);
    } else if (category === 'java') {
      setRenderedContent(<JavaComponent />);
    }
  };

  const clickDownBtn = () => {
    setIsCategroyVisible(true)
  }

  const clickUpBtn = () => {
    setIsCategroyVisible(false)
  }



  return (
    <div className='outBox w-full h-[95%] px-[5px] md:py-0 md:px-[20px]'>
      <div
        className={`w-[100%] relative h-[100%] flex flex-col items-center rounded-md text-center ${darkMode ? 'bg-slate-700' : 'bg-[#e8e6e4]'}`}>
        <section
          className={`fixed  flex flex-col justify-between w-[100%] px-[30px]  ${isCategoryVisible ? 'h-[95%] md:h-[200px]' : ''} ${darkMode ? 'bg-[#64748b]' : 'bg-[#c7c1bb]'} `}
          style={{ zIndex : 10 }}
        >
          <div className={`flex flex-col flex-wrap py-[20px] md:flex-row gap-2 items-center overflow-y-auto justify-center ${isCategoryVisible ? '' : 'hidden'}`}>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'javascript' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'}` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('javascript')}
            > 
              <JavaScriptbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px]  md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'typescript' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('typescript')}
            >
              <TypeScriptbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'python' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('python')}
            >
              <Pythonbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'java' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('java')}
            >
              <Javabutton/>
            </button>
          </div>
          <div className=''>
            <button
              className={`w-full ${isCategoryVisible ? '' : 'hidden'}`}
              onClick={clickUpBtn}
            >
                <FontAwesomeIcon icon={faAngleUp} />
              </button>
            <button
              className={`w-full flex flex-col items-center ${isCategoryVisible ? 'hidden' : ''}`}
              onClick={clickDownBtn}
            >
                <span className='my-[10px]'>Category</span>
                <FontAwesomeIcon icon={faAngleDown} />
              </button>
          </div>
        </section>
        <section className='w-full h-full mt-[60px]'>
          {renderedContent}
        </section>
      </div>
    </div>
  )
}

export default LanguageDocs