import React, { useState } from 'react';
import Frontend from './frontend/Frontend';
import Backend from './backend/Backend';
import { useDarkMode } from '../../context/Dark-mode';
import All from './All';

const Projects = () => {
const {darkMode} = useDarkMode()
const [selectedCategory, setSelectedCategory] = useState<string | null>('All');
const [renderedContent, setRenderedContent] = useState<React.ReactNode | null>(<All/>);

  const handleCategoryClick = (category: string) => {
    setSelectedCategory(category);

    if (category === 'Frontend') {
      setRenderedContent(<Frontend />);
    } else if (category === 'Backend') {
      setRenderedContent(<Backend />);
    } else if (category === 'All') {
      setRenderedContent(<All />);
    }
  };

  return (
    <div
      className='outBox w-full h-[90%] md:h-[93%] md:py-[0px] px-[12px] md:px-[20px]'
      style={{
        zIndex: 100,
      }}
    >
      <div className={`w-[100%] h-[99%] md:h-[95%] lg:h-[99%] rounded-lg text-center ${darkMode ? 'bg-slate-700' : 'bg-[#e8e6e4]'}`}>
        <section className='w-full h-[100%]'>
          <button
            className={`mx-[3px] my-[20px] md:m-[20px] p-[5px] w-[80px] md:w-[100px] rounded-md transition-all ${
              selectedCategory === 'All' ? `${darkMode ? 'bg-slate-400':'bg-[#86756b]'} text-white scale-97` : `  ${darkMode ? 'text-white shadow-slate-300 hover:bg-slate-400 ' : 'text-black shadow-black hover:bg-[#86756b] '} `
            }`}
            onClick={() => handleCategoryClick('All')}
          >
            All
          </button>
          <button
            className={`mx-[3px] my-[20px] md:m-[20px] p-[5px] w-[80px] md:w-[100px] rounded-md transition-all ${
              selectedCategory === 'Frontend' ? `${darkMode ? 'bg-slate-400':'bg-[#86756b]'} text-white scale-97` : `  ${darkMode ? 'text-white shadow-slate-300 hover:bg-slate-400 ' : 'text-black shadow-black hover:bg-[#86756b] '} `
            }`}
            onClick={() => handleCategoryClick('Frontend')}
          >
            FrontEnd
          </button>
          <button
            className={`mx-[3px] my-[20px] md:m-[20px] p-[5px] w-[80px] md:w-[100px] rounded-md transition-all ${
              selectedCategory === 'Backend' ? `${darkMode ? 'bg-slate-400':'bg-[#86756b]'}  text-white scale-97` : ` ${darkMode ? 'text-white shadow-slate-300 hover:bg-slate-400' : 'text-black shadow-black hover:bg-[#86756b]'} `
            }`}
            onClick={() => handleCategoryClick('Backend')}
          >
            BackEnd
          </button>
          {renderedContent}
        </section>
      </div>
    </div>
  );
};

export default Projects;
