import { faAnglesUp, faCaretUp } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { useEffect, useState } from 'react'
import { useDarkMode } from '../context/Dark-mode'

export const ScrollToTop = () => {
  const [isVisible, setIsVisible] = useState(false)
  const {darkMode} = useDarkMode()

  const toggleVisibility = () => {
    if (window.scrollY > 300) {
      setIsVisible(true)
    } else {
      setIsVisible(false)
    }
  }

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    })
  }

  useEffect(() => {
    window.addEventListener('scroll', toggleVisibility)

    return () => {
      window.removeEventListener('scroll', toggleVisibility)
    }
  }, [])
  
  
  return (
    <div
      className={`fixed bottom-5 right-5 ${isVisible ? 'opacity-100' : 'opacity-0'}`}
      style={{ zIndex:200 }}
    >
      <button
        type="button"
        onClick={scrollToTop}
        className=' bg-white shadow-md shadow-black inline-flex items-center justify-center rounded-full p-3 text-white transition-opacity focus:outline-none focus:ring-2 focus:ring-offset-2 w-8 h-8'
      >
        <FontAwesomeIcon
          icon={faAnglesUp}
          bounce
          className={` text-2xl ${darkMode ? 'text-slate-600' : 'text-[#86756b]'}`}
          style={{
            textShadow: '2px 2px 4px rgba(200, 200, 200, 1)',
          }}
        />
      </button>
    </div>
  )
}