import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { useDarkMode } from '../../../../context/Dark-mode'
import { useFrontendContext } from '../../../../context/FrontendContext';
import { components } from '../Frontend';
import { faCaretLeft, faCaretRight, faCircleLeft, faCircleRight } from '@fortawesome/free-solid-svg-icons';
import { CSSIcon, JavaScriptIcon, PhpIcon } from '../../../../icons/StackIcons';
import { useState } from 'react';
import PhotoModal from '../../../../modal/PhotoModal';
import { PersonalProjectIcon } from '../../ProjectStatus';


const imageComponents = [
  '/image/project/Frontend/Frontend_Basic/Frontend_Basic1.png',
  '/image/project/Frontend/Frontend_Basic/Frontend_Basic2.png',
  '/image/project/Frontend/Frontend_Basic/Frontend_Basic3.png',
  '/image/project/Frontend/Frontend_Basic/Frontend_Basic4.png',
  '/image/project/Frontend/Frontend_Basic/Frontend_Basic5.png',
  '/image/project/Frontend/Frontend_Basic/Frontend_Basic6.png',
];


const FrontendBasic = () => {

  const { darkMode } = useDarkMode()

  const { handleSwipeLeft, handleSwipeRight, currentIndex } = useFrontendContext();

  const isAtFirstPage = currentIndex === 0;
  const isAtLastPage = currentIndex === components.length - 1;

  const [currentImageIndex, setCurrentImageIndex] = useState(0);

  const isAtFirstImage = currentImageIndex === 0;
  const isAtLastImage = currentImageIndex === imageComponents.length - 1;

  const [isPhotoModalOpen, setPhotoModalOpen] = useState(false);
  const [whichPhoto, setWhichPhoto] = useState('');

  const openPhotoModal = () => {
    setPhotoModalOpen(true);
  };

  const closePhotoModal = () => {
    setPhotoModalOpen(false);
  };

  const clickPhotoModal = (photo:string) => {
    setWhichPhoto(photo)
  }

  const handleNextImage = () => {
    if (currentImageIndex < imageComponents.length - 1) {
      setCurrentImageIndex(currentImageIndex + 1);
    }
  };

  const handlePreviousImage = () => {
    if (currentImageIndex > 0) {
      setCurrentImageIndex(currentImageIndex - 1);
    }
  };

  const clickLinkBtn = () => {
    window.open("http://jjdong2.dothome.co.kr/basic", "_blank");
  }
  
  return (

    <div className='p-[14px] md:p-[30px] relative w-[100%] h-[96%] md:h-[100%]'>
      <div className={`flex flex-row items-center justify-center border-b-2 py-[20px] pt-0 ${darkMode ? 'border-slate-500' : 'border-slate-300'}`}>
        <div>
          {
            isAtFirstPage ?
              (
                <button className='block translate-x-[-20px] opacity-30 '>
                  <FontAwesomeIcon icon={faCaretLeft} className='md:ml-[20px] text-3xl hover:scale-150 transition-all' />
                </button>
              )
                :
              (
                <button className='block translate-x-[-20px] ' onClick={handleSwipeLeft}>
                  <FontAwesomeIcon icon={faCaretLeft} className='md:ml-[20px] text-3xl hover:scale-150 transition-all' />
                </button>
              ) 
          }
        </div>
        <h2 className="text-[13px] md:text-xl lg:text-2xl text-center w-[200px] sm:w-[300px] lg:w-[400px] font-bold">Basic Site Project (FE)</h2>
        <div>
          {
            isAtLastPage ?
              (
                <button className='block translate-x-[20px] opacity-30'>
                  <FontAwesomeIcon icon={faCaretRight} className='md:mr-[20px] text-3xl hover:scale-150 transition-all' />
                </button>
              )
                :
              (
                <button className='block translate-x-[20px]' onClick={handleSwipeRight}>
                  <FontAwesomeIcon icon={faCaretRight} className='md:mr-[20px] text-3xl hover:scale-150 transition-all' />
                </button>
              )
          }
        </div>
      </div>
      <div className='w-full h-[94%] md:h-[95%] lg:h-[97%] overflow-y-auto overflow-x-hidden'>
        <PhotoModal photo={whichPhoto} isModalOpen={isPhotoModalOpen} onClose={closePhotoModal}/>
        <div className='flex flex-col lg:flex-row text-sm md:text-[17px] lg:min-h-[900px]'>
          <section className={`leftSection flex flex-col w-full lg:items-start lg:w-[40%] lg:mb-[30px] lg:min-w-[350px] lg:border-r-2 ${darkMode ? 'border-slate-500' : 'border-slate-300'}`}>
            <div className={`sticky flex gap-1 pt-[10px]`}>
              <PersonalProjectIcon />
            </div>
            <ul className='text-start list-inside list-disc mt-[30px] text-[15px]'>
              <li className='mb-[10px]'>
                <span className='text-start mt-[30px] font-semibold'>직책 : </span>
                <span>팀원</span>
              </li>
              <li className='mb-[10px]'>
                <span className='text-start font-semibold'>프로젝트 기간 : 5주</span>
                <span className='ml-[10px] hidden md:inline-block'>(2023.09.06 ~ 2023.10.10)</span>
                <p className='md:hidden ml-[30px]'>(2023.09.06 ~ 2023.10.10)</p>
              </li>
              <li className='mb-[10px]'>
                <span className='text-start font-semibold'>참여 인원: 3명</span>
                <span className='ml-[10px]'>(FullStack 3)</span>
              </li>
            </ul>
            <div className='flex flex-col items-center mt-[30px] lg:mt-[90px] w-[100%]'>
              <div className='w-[80%] flex items-center justify-between'>
                {isAtFirstImage ?
                  (<button onClick={handlePreviousImage}>
                    <FontAwesomeIcon icon={faCircleLeft} className='text-[30px] opacity-30 mr-[15px]' />
                  </button>)
                  :
                  (<button onClick={handlePreviousImage}>
                    <FontAwesomeIcon icon={faCircleLeft} className={`text-[30px] mr-[15px] ${darkMode ? 'hover:text-blue-500' : 'hover:text-[#86756b]'}`} />
                  </button>)
                  }
                <button
                  className='hover:scale-95 hover:opacity-90 transition-all w-[80%]'
                  onClick={() => {
                    openPhotoModal();
                    clickPhotoModal(imageComponents[currentImageIndex])
                }}
                >
                  <img src={imageComponents[currentImageIndex]} alt="" className={`w-full shadow-md ${darkMode ?'shadow-slate-500':'shadow-slate-400'} `}/>
                </button>
                {isAtLastImage ?
                  (<button onClick={handleNextImage}>
                    <FontAwesomeIcon icon={faCircleRight} className='text-[30px] ml-[15px] opacity-30' />
                  </button>)
                  : 
                  (<button onClick={handleNextImage}>
                    <FontAwesomeIcon icon={faCircleRight} className={`text-[30px] ml-[15px] ${darkMode ? 'hover:text-blue-500' : 'hover:text-[#86756b]'}`} />
                  </button>)
                  }
              </div>
              <div className='mt-[30px]'>
                <button
                  className='hover:text-blue-500 hover:underline ml-[20px]'
                  onClick={clickLinkBtn}
                >
                  사이트 바로가기 </button>
              </div>
            </div>
          </section>
          <section className='rightSection flex flex-col items-start lg:ml-[30px] w-full lg:w-[70%]'>
            <p className='underline text-start lg:text-center my-[30px]'>React.js 라이브러리와 TailwindCSS를 활용한 가상화폐 차트뷰 사이트입니다.</p>
            <div className='mb-[30px]'>
              <h3 className="text-lg font-semibold mt-4 mb-2 text-start">개발환경</h3>
              <ul className="list-disc list-inside text-start text-[15px]">
                <li className='mb-[10px] ml-[20px] flex items-center gap-1'>
                  <span>FE </span>
                  : <PhpIcon /> <JavaScriptIcon/> <CSSIcon />
                </li>
              </ul>
            </div>
            <div className='mb-[30px]'>
              <h3 className="text-lg font-semibold mt-4 mb-2 text-start">프론트 담당 역할</h3>
              <ul className="list-disc list-inside text-start text-[15px]">
                <li className='mb-[10px] ml-[20px]'>
                  <span className='font-semibold'>회원가입 API 개발</span>
                  <p className='ml-[30px]'>무작위로 회원가입 하는것을 방지하는 목적으로 이메일을 통한 가입 로직 구현 </p>
                  <p className='ml-[30px]'> → 인증코드를 발송, 대조하여 회원가입 기능 구현</p>
                </li>
                <li className='mb-[10px] ml-[20px]'>
                  <span className='font-semibold'>로그인 API 개발</span>
                  <p className='ml-[30px]'>위키 정보에 기여(contribute)할 수 있게 회원제를 운영 </p>
                  <p className='ml-[30px]'> → 토큰 기반 인증을 이용하여 유저 혹은 위키정보관리 시스템 구축</p>
                </li>
                <li className='mb-[10px] ml-[20px]'>
                  <span className='font-semibold'>회원탈퇴 API 개발</span>
                  <p className='ml-[30px]'>회원 개인 정보를 삭제하기 희망하는 회원을 위하여 이메일을 통한 회원탈퇴 로직 구현mn</p>
                  <p className='ml-[30px]'> → 인증코드를 발송, 대조하여 회원가입 기능 구현</p>
                </li>
                <li className='mb-[10px] ml-[20px]'>
                  <span className='font-semibold'>프로필 저장기능 구현</span>
                  <p className='ml-[30px]'>회원 정보중 프로필 사진을 저장혹은 수정기능 구현 </p>
                  <p className='ml-[30px]'> → Google cloud storage 를 통해 프로필 사진 저장 시스템 구축</p>
                </li>
                <li className='mb-[10px] ml-[20px]'>
                  <span className='font-semibold'>ElasticSearch 활용한 mypost 찾기 기능 구현</span>
                  <p className='ml-[30px]'>ElasticSearch에 query를 작성</p>
                </li>
              </ul>
            </div>
            <div>
              <h3 className="text-lg font-bold mt-4 text-start">link</h3>
              <ul className='text-start mt-[10px] text-[15px]'>
                <li className='mb-[10px] ml-[20px]'>
                  <h4>Github: <a href="https://github.com/Ju-jh/Coin_Market_FE" className='text-blue-500 hover:underline'>Github URL</a></h4>
                </li>
                <li className='mb-[10px] ml-[20px]'>
                  <h4>Brochure: <a href="https://www.notion.so/AmuWiki-87-4c216ba5fe50498280e3c6a252286d55?pvs=4 " className='text-blue-500 hover:underline'>Brochure URL</a></h4>
                </li>
                <li className='ml-[20px]'>
                  <h4>Blog 회고: <a href="https://jrogrammer.tistory.com/238" className='text-blue-500 hover:underline'>Blog 회고 URL</a></h4>
                </li>
              </ul>
            </div>
          </section>
        </div>
      </div>
    </div>
  )
}

export default FrontendBasic