
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent06 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/**
  중복된 데이터를 배제하고 조회 : DISTINCT
    형식 : SELECT {DISTINCT} 컬럼리스트 FROM 테이블명
*/
-- 사원테이블에 존재하는 모든 부서번호를 조회
SELECT DISTINCT dept_id FROM employee;
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent06