
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent10 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/**
	내장 함수 사용 : 숫자함수, 문자함수, 집계(그룹)함수, 날짜함수...
    형식 : 컬럼리스트, 조건절
*/
-- 숫자함수
-- 함수 테스트 테이블 : Dual
-- 123, -123 절대값 출력 : ABS()
SELECT ABS(123), ABS(-123) FROM dual;
SELECT ABS(123), ABS(-123);

-- 소수점 버리기 : FLOOR(), TRUNCATE()
SELECT FLOOR(123.456), FLOOR(3843.384349534);
SELECT TRUNCATE(123.456, 1), TRUNCATE(3843.384349534, 0);

-- 임의의 숫자 생성 : RAND()
SELECT FLOOR(RAND()*1000) AS num1, TRUNCATE(RAND()*1000, 2) AS num2;

-- 나머지 연산 : MOD()
SELECT MOD(20,2), MOD(21,2);
SELECT MOD(FLOOR(RAND()*1000),2) AS number1;

-- 문자함수
-- 문자열 결합 : CONCAT()
SELECT CONCAT('my','sq','l');

-- 홍길동(S0001) --> emp_name
-- 홍길동(S0001) 사원만 조회
SELECT CONCAT(emp_name,'(',emp_id,')') AS emp_name, hire_date, salary
 FROM employee
 WHERE CONCAT(emp_name,'(',emp_id,')') = '홍길동(S0001)';
 
-- 문자열 추출 : SUBSTRING(문자열, 위치, 문자열수)
SELECT SUBSTRING('대한민국', 1,2);
SELECT SUBSTRING('대한민국 홍길동', 6,3);

-- 사원아이디, 사원명, 입사년도, 폰번호, 이메일 조회
SELECT emp_id, emp_name
		,SUBSTRING(hire_date,1,4) AS year
        ,SUBSTRING(hire_date,6,2) AS mm
        ,SUBSTRING(hire_date,9,2) AS dd
        , phone, email
 FROM employee;
 
 -- 2015년도 입사자들 모두 조회
 SELECT * FROM employee
 WHERE SUBSTRING(hire_date,1,4) = '2015';
 
 -- 1월에 입사한 사원들 조회
 SELECT * FROM employee
 WHERE SUBSTRING(hire_date,6,2) = '01';
 
 -- 2018년도에 입사한 정보시스템 부서 사원 조회
 SELECT * FROM employee
 WHERE SUBSTRING(hire_date,1,4) = '2018' AND dept_id='SYS';
 
-- 오른쪽, 왼쪽 기준 문자열 추출 : LEFT('문자열',숫자), RIGHT('문자열',숫자)
 SELECT LEFT('대한민국 홍길동 만세!!!', 4), RIGHT('대한민국 홍길동 만세!!!', 4);
 
-- 2018년도에 입사한 정보시스템 부서 사원 조회
 SELECT * FROM employee
 WHERE LEFT(hire_date,4) = '2018' AND dept_id='SYS';
 
 -- 대,소문자 변경 : UPPER('문자열'), LOWER('문자열')
 SELECT UPPER('abs'), LOWER('ABC');
 
 -- 검색하는 데이터가 소문자로 전달된 경우 : SYS
 SELECT * FROM employee WHERE dept_id = UPPER('sys');
 
 -- 공백 삭제 : TRIM('공백포함 문자열')
SELECT TRIM('       대한민국      ');
SELECT LTRIM('       대한민국      ');
SELECT RTRIM('       대한민국      ');
SELECT TRIM(LEADING '!' FROM '!!!!!!!!대한민국      ');
SELECT TRIM(TRAILING '!' FROM '대한민국!!!!!!!!');
 
 -- 문자열 포맷
 SELECT FORMAT(12345678, 0);
 
 -- 사원아이디, 사원명, 입사년도, 연봉을 3자리로 구분하여 출력
SELECT emp_id, emp_name, CONCAT(LEFT(hire_date,4),'년도') AS hire_date,
		CONCAT(FORMAT(salary,0),'만원') AS salary
FROM employee;        

-- 날짜 함수 : CURDATE(), NOW()
SELECT CURDATE();  -- 년, 월, 일
SELECT NOW();	   -- 2023-10-12 15:05:20 함수기준으로 실행

SELECT NOW(), SLEEP(2), NOW(); -- 2023-10-12 15:08:33	0	2023-10-12 15:08:35
SELECT NOW(), SLEEP(2), NOW(); -- 2023-10-12 15:08:07	0	2023-10-12 15:08:07

-- 형변환 함수 : CAST(변환하고자하는 데이터 AS 변환데이터타입)
-- 			, CONVERT(변환하고자하는 데이터 AS 변환데이터타입) 
SELECT CAST(20231012 AS CHAR); -- 숫자 --> 문자
SELECT CAST(20231012 AS DATE); -- 숫자 --> 날짜(DATE)
SELECT CAST('20231012' AS DATE); -- 문자 --> 날짜(DATE)

-- 123456789 숫자를 문자로 변환 후 3자리숫자로 구분
SELECT FORMAT(CAST(123456789 AS CHAR),0);
SELECT FORMAT(123456789, 0) ;

-- 현재 날짜를 가져와서(년,월,일) 문자로 변경, 다시 날짜타입(DATETIME)으로 변경
SELECT CAST(CAST(CURDATE() AS CHAR) AS DATETIME);

-- 현재 날짜를 가져와서(년,월,일,시,분,초) 문자로 변경, 다시 날짜타입(DATETIME)으로 변경
SELECT CAST(CAST(NOW() AS CHAR) AS DATETIME);

-- employee 테이블의 입사일이 저장될때 무슨 함수를 사용했을까요?
SELECT emp_name, hire_date, CAST(hire_date AS DATETIME) FROM employee;

-- 입력폼에서 '20150101' 문자열 타입으로 전송되어진 경우, 해당일의 입사자를 모두 조회
SELECT * FROM employee
WHERE hire_date = CAST('20150101' AS DATE);

SELECT * FROM employee
WHERE hire_date = '20150101';

DESC employee;
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent10