import React, { createContext, useContext, useState, ReactNode } from 'react';
import { components } from '../component/projects/All';

const AllContext = createContext<{ currentIndex: number; handleSwipeLeft: () => void; handleSwipeRight: () => void } | undefined>(undefined);

interface AllProviderProps {
  children: ReactNode;
}

export const AllProvider: React.FC<AllProviderProps> = ({ children }) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const handleSwipeLeft = () => {
    setCurrentIndex((prevIndex) => (prevIndex - 1 + components.length) % components.length);
  };

  const handleSwipeRight = () => {
    setCurrentIndex((prevIndex) => (prevIndex + 1) % components.length);
  };

  return (
    <AllContext.Provider value={{ currentIndex, handleSwipeLeft, handleSwipeRight }}>
      {children}
    </AllContext.Provider>
  );
};

export const useAllContext = () => {
  const context = useContext(AllContext);
  if (context === undefined) {
    throw new Error('useFrontendContext must be used within a AllProvider');
  }
  return context;
};
