
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent11 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/**
	그룹(집계) 함수 
    1. COUNT(), SUM(), AVG(), MAX(), MIN()...
    2. GROUP BY - 그룹함수가 적용되기 전 그룹화하는 작업 기준
    3. HAVING - 그룹함수에 적용하는 조건절
*/
-- 1. 그룹함수 : 형식 - 컬럼리스트가 위치하는 곳에 함수 호출
SELECT COUNT(*) FROM employee;	-- 20
SELECT COUNT(salary) FROM employee; -- 19
SELECT COUNT(IFNULL(salary, 500)) FROM employee;  -- 20
SELECT emp_name, salary FROM employee WHERE salary IS NULL;

SELECT COUNT(emp_id) FROM employee; -- 전체데이터 기준 : *, 기본키컬럼

-- SUM(숫자데이터 컬럼)
-- 총급여를 출력, 3자리로 구분, '만원' 단위 추가
SELECT CONCAT(FORMAT(SUM(salary), 0), '만원') AS sum FROM employee;

-- AVG(숫자데이터 컬럼)
-- 평균급여를 출력, 3자리 구분, '만원' 단위 추가
-- 단, 소수점이 있는 경우 모두 절삭
SELECT CONCAT(FORMAT(FLOOR(AVG(salary)), 0), '만원') AS '평균 급여' FROM employee;
SELECT AVG(salary) FROM employee;

-- 총급여, 평균급여 출력
SELECT CONCAT(FORMAT(SUM(salary), 0), '만원') AS '총급여'
		, CONCAT(FORMAT(FLOOR(AVG(salary)), 0), '만원') AS '평균급여'
FROM employee;  

-- 총연봉, 평균연봉, 최대연봉, 최저연봉      
SELECT SUM(salary), AVG(salary), MAX(salary), MIN(salary)
FROM employee;

-- 가장빠른 입사일, 가장최근 입사일
SELECT MIN(hire_date), MAX(hire_date) FROM employee;

-- 부서별 총연봉, 평균연봉, 최대연봉, 최저연봉
SELECT * FROM employee ORDER BY dept_id;
SELECT dept_id, SUM(salary), AVG(salary), MAX(salary), MIN(salary)
FROM employee
GROUP BY dept_id; -- GROUP BY 기준 컬럼순으로 내부 정렬이 진행됨
-- ORDER BY dept_id ;

-- 기본키의 GROUP BY는 개별적인 데이터기준으로 그룹핑됨
SELECT emp_id, SUM(salary), AVG(salary) 
FROM employee
GROUP BY emp_id;

-- 입사년도별 총연봉, 평균연봉, 최대연봉, 최저연봉
SELECT LEFT(hire_date, 4) AS 입사년도,
		dept_id,
        COUNT(*), SUM(salary), AVG(salary), MAX(salary), MIN(salary)
FROM employee
GROUP BY LEFT(hire_date, 4), dept_id;

-- 입사월별 사원수, 총연봉, 평균연봉, 최대연봉, 최저연봉
SELECT SUBSTRING(hire_date, 6, 2) AS 입사월,
		dept_id,  		
        COUNT(*) AS 사원수, SUM(salary), AVG(salary), MAX(salary), MIN(salary)
FROM employee
GROUP BY SUBSTRING(hire_date, 6, 2), dept_id
ORDER BY SUBSTRING(hire_date, 6, 2) ASC;

-- 부서별 사원수가 2명인 부서만 출력
SELECT dept_id, COUNT(*)
FROM employee
GROUP BY dept_id
HAVING COUNT(*) = 2;
 
SELECT dept_id, COUNT(*) AS 사원수
FROM employee
GROUP BY dept_id
HAVING 사원수 = 2;
 
-- 입사년도별 평균급여가 5000 이상인 해당년도만 출력
SELECT LEFT(hire_date, 4) AS year, FLOOR(AVG(salary))
FROM employee
GROUP BY year
HAVING AVG(salary) >= 5000;
 
-- 부서별 최고급여가 6000 이상 7000미만인 부서 출력
SELECT dept_id, MAX(salary) AS max
FROM employee
GROUP BY dept_id
HAVING max BETWEEN 6000 AND 6999 ;
-- HAVING max >= 6000 AND max < 7000;
 
-- ROLLUP 함수를 사용하여 총합계, 소계를 출력
-- 부서별 총합계를 출력, 3자리 구분
-- 단, 사원이 없는 부서는 그룹핑하지 않음
SELECT dept_id, FORMAT(SUM(salary), 0)
FROM employee
WHERE salary IS NOT NULL
GROUP BY dept_id WITH ROLLUP;
 
SELECT * FROM department;
 
-- 년도별, 각 부서의 사원수와 총연봉을 출력
SELECT LEFT(hire_date, 4) AS year, dept_id, COUNT(*) AS 사원수, SUM(salary) AS 총연봉
FROM employee
WHERE salary IS NOT NULL
GROUP BY year, dept_id WITH ROLLUP;

-- 부서별, 사원수(성별구분) 출력
SELECT dept_id, gender, COUNT(*)
FROM employee
GROUP BY dept_id, gender WITH ROLLUP;

-- 부서별 총연봉 출력
SELECT IF(GROUPING(dept_id), '총합계', IFNULL(dept_id, '-')) AS 부서, 
		SUM(salary) AS 총합계
FROM employee
WHERE salary IS NOT NULL
GROUP BY dept_id WITH ROLLUP;
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent11