import { useState } from 'react';
import { useDarkMode } from '../../../../../../context/Dark-mode';
import './dockerhub.css'
import { faAnglesLeft, faBars } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import DockerHubComponent01 from './category/01';
import DockerHubComponent02 from './category/02';




const DockerHubComponent = () => {

  const { darkMode } = useDarkMode();
  const [content, setContent] = useState<React.ReactNode | null>(<DockerHubComponent01/>);
  const [activeButton, setActiveButton] = useState<number>(1);
  
  const clickBtn = (category: string, number: number) => {
    if (category === 'dockerhub01') {
      setContent(<DockerHubComponent01 />)
      setActiveButton(number)
    } else if (category === 'dockerhub02') {
      setContent(<DockerHubComponent02 />)
      setActiveButton(number)      
    }

  }

  const [isMenuActive, setIsMenuActive] = useState<boolean>(true)

  const openMenuBtn = () => {
    setIsMenuActive(true)
  }

    const closeMenuBtn = () => {
    setIsMenuActive(false)
  }

  return (
    <div className='outBox w-full h-[100%] px-[5px] md:py-0 md:px-[20px]'>
      <div className={`w-[100%] h-[90%] lg:h-[93%] text-center ${darkMode ? 'bg-slate-700' : 'bg-[#e8e6e4]'} `}>
        <section className='w-full  bg-white h-[100%]  flex items-start justify-start'>
          <div className={`bg-white h-[100%] border-r-2 border-slate-300 font-bold transition-all ${isMenuActive ? ' min-w-[280px]':' w-[0px]'}`}>
            <div className={`flex flex-col items-start  h-full p-[30px] md:py-[50px] text-[13px] md:text-[15px]  ${darkMode ? 'bg-slate-400 text-slate-900' : 'bg-[#86756b] text-white'} `}>
              <div className='w-full flex items-center px-[10px] mb-[30px]'>
                <span className={` w-full text-start text-[20px] ${isMenuActive ? 'block' : 'hidden'}`}>목차</span>
                {isMenuActive && <button
                    onClick={closeMenuBtn}
                    className='hover:bg-slate-200 rounded-md p-[10px]'
                  >
                    <FontAwesomeIcon icon={faAnglesLeft} className='text-[18px]' />
                  </button>}
              </div>
              <ol className='w-full text-start h-full overflow-y-auto md:ml-[15px] lg:ml-[20px]'>
                <li className='flex flex-col items-start'>
                  <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all sm:text-[13px] ${activeButton === 1 ? `${darkMode ? 'text-blue-600' : 'text-blue-400'}` : ''}`} onClick={() => { clickBtn('dockerhub01', 1) }}>01 What is Docker Hub?</button>
                  <button className={`mb-[10px] hover:text-blue-500 hover:underline text-start transition-all sm:text-[13px] ${activeButton === 2 ? `${darkMode ? 'text-blue-600' : 'text-blue-400'}` : ''}`} onClick={() => { clickBtn('dockerhub02', 2) }}>02 Hot to use Docker Hub?</button>
                </li>
              </ol>
            </div>
          </div>
          <div className='absolute p-[20px] pr-0'>
            { !isMenuActive  && <button
              onClick={openMenuBtn}
              className=' '
                >
                  <FontAwesomeIcon icon={faBars} className={`text-[24px] ${darkMode ? 'text-white' : 'text-black'}`} />
            </button>}
          </div>
          <div className={`w-full h-full pt-[60px] p-[20px] md:p-[50px] overflow-y-auto text-start ${darkMode ? 'bg-slate-900 text-slate-100' : 'bg-white text-black'}`}>
            {content}
          </div>
        </section>
      </div>
    </div>
  )
}

export default DockerHubComponent