
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent14 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/**
	서브쿼리 : 메인쿼리(출력되는 컬럼리스트)에 서브쿼리를 추가하여 실행하는 형식
	select 컬럼리스트       from 테이블명   where 조건절
          (스칼라 서브쿼리)      (인라인뷰)        (서브쿼리)
*/  
-- 홍길동 사원이 속한 부서의 이름을 출력해주세요
SELECT DEPT_NAME
FROM department
WHERE DEPT_ID = (SELECT DEPT_ID FROM EMPLOYEE WHERE EMP_NAME='홍길동');

-- 홍길동 사원이 사용한 휴가 내역을 조회
SELECT * 
FROM VACATION
WHERE EMP_ID = (SELECT EMP_ID FROM EMPLOYEE WHERE EMP_NAME = '홍길동');

SELECT EMP_ID FROM EMPLOYEE WHERE EMP_NAME = '홍길동';

-- 3본부에 속해있는 부서들을 조회
SELECT *
FROM DEPARTMENT
WHERE UNIT_ID = (SELECT UNIT_ID FROM UNIT WHERE UNIT_NAME = '제3본부');

SELECT UNIT_ID FROM UNIT WHERE UNIT_NAME = '제3본부';

-- 3본부에 속해있는 모든 사원들을 조회
-- 다중행 서브쿼리 : 서브쿼리를 실행한 결과가 2행 이상 출력되는 경우
SELECT *
FROM EMPLOYEE
WHERE DEPT_ID IN (SELECT DEPT_ID
					FROM DEPARTMENT
					WHERE UNIT_ID = (SELECT UNIT_ID FROM UNIT WHERE UNIT_NAME = '제3본부'));

-- 가장 먼저 입사한 사원의 정보를 출력
SELECT * 
FROM EMPLOYEE
WHERE HIRE_DATE = (SELECT MIN(HIRE_DATE) FROM EMPLOYEE);
SELECT MIN(HIRE_DATE) FROM EMPLOYEE;

-- 휴가를 간 적이 있는 정보시스템 부서의 사원들을 출력
SELECT * FROM EMPLOYEE
WHERE DEPT_ID = (SELECT DEPT_ID FROM DEPARTMENT WHERE DEPT_NAME='정보시스템')
	AND EMP_ID IN (SELECT DISTINCT EMP_ID FROM VACATION);

SELECT DEPT_ID FROM DEPARTMENT WHERE DEPT_NAME='정보시스템';
SELECT DISTINCT EMP_ID FROM VACATION;
-- Subquery returns more than 1 row	0.000 sec : 다중행 출력 에러

-- 휴가를 간 적이 없는 정보시스템 부서의 사원들을 출력
SELECT * FROM EMPLOYEE
WHERE DEPT_ID = (SELECT DEPT_ID FROM DEPARTMENT WHERE DEPT_NAME='정보시스템')
	AND EMP_ID NOT IN (SELECT DISTINCT EMP_ID FROM VACATION);
    
    
-- 사원별 휴가사용 일수를 그룹핑하여,  사원아이디, 사원명, 입사일, 연봉, 휴가사용일수를 조회해주세요.    
-- 휴가사용 일수를 구하는 인라인뷰와 사원테이블을 조인
SELECT E.EMP_ID, E.EMP_NAME, E.HIRE_DATE, E.SALARY, V.DURATION
	FROM EMPLOYEE E 
		INNER JOIN (SELECT EMP_ID, SUM(DURATION) AS DURATION 
							FROM VACATION GROUP BY EMP_ID) AS V
		ON E.EMP_ID = V.EMP_ID ;  -- 14


-- 휴가 사용일수와 사원정보, 단 모든 사원의 정보 조회
-- 현 시점 휴가 사용일수가 없는 사원은 0으로 조회
SELECT E.EMP_ID, E.EMP_NAME, E.HIRE_DATE, 
		IFNULL(E.SALARY, 0) SALARY, IFNULL(V.DURATION, 0) DURATION
	FROM EMPLOYEE E 
		LEFT OUTER JOIN (SELECT EMP_ID, SUM(DURATION) AS DURATION 
							FROM VACATION GROUP BY EMP_ID) AS V
		ON E.EMP_ID = V.EMP_ID ;  -- 20(14 + 6)



SELECT COUNT(*)
FROM (SELECT E.EMP_ID, E.EMP_NAME, E.HIRE_DATE, E.SALARY, V.DURATION
	FROM EMPLOYEE E 
		INNER JOIN (SELECT EMP_ID, SUM(DURATION) AS DURATION 
							FROM VACATION GROUP BY EMP_ID) AS V
		ON E.EMP_ID = V.EMP_ID ) EV;  -- 14

SELECT COUNT(*) FROM EMPLOYEE;  -- 20     


-- MYSHOP2019
USE MYSHOP2019;
SELECT database();   

-- CATEGORY, SUB_CATEGORY, PRODUCT  INNER JOIN
SELECT COUNT(*) FROM CATEGORY;  -- 3
SELECT COUNT(*) FROM SUB_CATEGORY;  -- 7
SELECT COUNT(*) FROM PRODUCT;  -- 41

-- 카테고리별 상품명을 조회
SELECT C.CATEGORY_ID, C.CATEGORY_NAME, SC.SUB_CATEGORY_ID, P.PRODUCT_NAME
FROM CATEGORY C INNER JOIN SUB_CATEGORY SC ON C.CATEGORY_ID = SC.CATEGORY_ID
				INNER JOIN PRODUCT P ON SC.SUB_CATEGORY_ID = P.SUB_CATEGORY_ID ;

SELECT COUNT(*)
FROM (SELECT C.CATEGORY_ID, C.CATEGORY_NAME, SC.SUB_CATEGORY_ID, P.PRODUCT_NAME
			FROM CATEGORY C , SUB_CATEGORY SC, PRODUCT P
			WHERE C.CATEGORY_ID = SC.CATEGORY_ID
				AND SC.SUB_CATEGORY_ID = P.SUB_CATEGORY_ID) A;    

-- 2018년도 기준 상품별 주문건수 조회 - 주문날짜(년도), 상품명, 총주문건수    
SELECT ROW_NUMBER() OVER(ORDER BY ORDER_DATE) AS NUMBER,
		ORDER_DATE, PRODUCT_NAME, ORDER_QTY
FROM (SELECT LEFT(OH.ORDER_DATE, 4) ORDER_DATE , P.PRODUCT_NAME
		, SUM(OD.ORDER_QTY) ORDER_QTY 
		FROM ORDER_DETAIL OD, ORDER_HEADER OH, PRODUCT P
		WHERE OD.ORDER_ID = OH.ORDER_ID
			AND OD.PRODUCT_ID = P.PRODUCT_ID
			AND LEFT(ORDER_DATE, 4) = '2018' 
			GROUP BY LEFT(OH.ORDER_DATE, 4), P.PRODUCT_NAME) A ;

-- 행번호 생성 함수
SELECT ROW_NUMBER() OVER(ORDER BY CUSTOMER_ID) AS NUMBER
		, CUSTOMER_NAME
 FROM CUSTOMER;  
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent14