
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent20 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/*
고객이 어떤상품 주문했는지 조회 : 고객아이디, 고객명, 주문아이디, 상품명, 서브카테고리명, 카테고리명
-- 상품의 카테고리 포함
-- 주문을 관리하는 사원정보 포함
*/
CREATE VIEW MGM_PRODUCT
AS
SELECT C.CUSTOMER_ID, C.CUSTOMER_NAME, OH.ORDER_ID, P.PRODUCT_NAME, SC.SUB_CATEGORY_NAME
		, CA.CATEGORY_NAME, E.EMPLOYEE_ID, E.EMPLOYEE_NAME
	FROM CUSTOMER C, ORDER_HEADER OH, ORDER_DETAIL OD, PRODUCT P
			, SUB_CATEGORY SC, CATEGORY CA, EMPLOYEE E
	WHERE C.CUSTOMER_ID = OH.CUSTOMER_ID
		AND OH.ORDER_ID = OD.ORDER_ID
        AND OD.PRODUCT_ID = P.PRODUCT_ID
        AND P.SUB_CATEGORY_ID = SC.SUB_CATEGORY_ID
        AND SC.CATEGORY_ID = CA.CATEGORY_ID
        AND OH.EMPLOYEE_ID = E.EMPLOYEE_ID ;

SELECT * FROM INFORMATION_SCHEMA.VIEWS WHERE TABLE_NAME ='MGM_PRODUCT';     

SELECT * FROM CUSTOMER;   
-- 최천사 고객이 주문한 상품내역
SELECT * FROM MGM_PRODUCT WHERE CUSTOMER_NAME ='최천사';

-- 고객별 상품 주문한 건수
SELECT ROW_NUMBER() OVER(ORDER BY COUNT(*) DESC) AS RNO, CUSTOMER_NAME, COUNT(*) ORD_COUNT
	FROM MGM_PRODUCT 
    GROUP BY CUSTOMER_ID, CUSTOMER_NAME;
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent20