
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent16 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/*
-- 데이터 생성(추가) 
-- 형식 : INSERT INTO [테이블명] {(컬럼리스트)}
-- 		  VALUES (데이터리스트) ;
*/
INSERT INTO EMP (EMP_ID, EMP_NAME, HIRE_DATE, SALARY)
	VALUES ('hong', '홍길동', CURDATE(), 1234);
SELECT * FROM EMP;
COMMIT;  -- 트랜잭션 완료

INSERT INTO EMP (EMP_ID, EMP_NAME, HIRE_DATE, SALARY)
	VALUES ('hong1', '홍길동1', CURDATE(), 1234);    
SELECT * FROM EMP;

INSERT INTO EMP (EMP_NAME, HIRE_DATE, SALARY, EMP_ID)
	VALUES ('테스트', CURDATE(), 8500, 'test');    
SELECT * FROM EMP;    

INSERT INTO EMP VALUES ('test2', '테스트', CURDATE(), 8500);  -- 컬럼리스트 생략시 DESC 구조로 체크
DESC EMP;  

INSERT INTO EMP VALUES ('test2', '테스트', CURDATE(), NULL);
SELECT * FROM EMP;
-- ROLLBACK;  -- SET AUTOCOMMIT(TRUE);
COMMIT;

-- CONSTRAINT(제약사항) : INSERT, UPDATE, DELETE
-- 중복 체크 : UNIQUE
-- NULL 체크 : NOT NULL
-- 기본키 제약(UNIQUE + NOT NULL) : PRIMARY KEY
-- 참조키 제약 : FOREIGN KEY -  관련있는 다른 테이블의 PRIMARY KEY를 참조하는 제약
-- DEFAULT, CHECK

-- 테이블 생성 시 제약사항 추가
-- EMP2 테이블에 제약사항 : EMP_ID(문자 4, 기본키 제약), EMP_NAME(문자10, NOT NULL)
CREATE TABLE EMP2(
	EMP_ID	CHAR(4)		PRIMARY KEY,
    EMP_NAME VARCHAR(10)	NOT NULL,
    HIRE_DATE DATE,
    SALARY INT
);
SELECT * FROM INFORMATION_SCHEMA.TABLES WHERE TABLE_SCHEMA ='HRDB2019';
DESC EMP2;
INSERT INTO EMP2 (EMP_ID, EMP_NAME, HIRE_DATE, SALARY)
	VALUES('tt', '아무개', CURDATE(), 8500);
SELECT * FROM EMP2;    

-- 제약사항 조회
DESC INFORMATION_SCHEMA.TABLE_CONSTRAINTS;
SELECT * FROM INFORMATION_SCHEMA.TABLE_CONSTRAINTS
	WHERE TABLE_SCHEMA = 'HRDB2019';

-- EMP3 테이블에 제약사항 : 
-- 제약사항 이름 규칙 : 테이블명_컬럼명_제약사항명 
-- EMP_ID(문자 4, 기본키 제약), EMP_NAME(문자10, NOT NULL)
CREATE TABLE EMP3(
	EMP_ID	CHAR(4),
    EMP_NAME VARCHAR(10) NOT NULL,
    HIRE_DATE DATE,
    SALARY INT,
    CONSTRAINT EMP3_EMP_ID_PK PRIMARY KEY(EMP_ID)
);
DESC EMP3;
SELECT * FROM INFORMATION_SCHEMA.TABLE_CONSTRAINTS 
WHERE TABLE_NAME = 'EMP3';

INSERT INTO EMP3 VALUES('hong', '홍길동', CURDATE(), 3000);
SELECT * FROM EMP3;

-- 참조키 제약 설정 : 두 개이상의 참조관계가 설정되어 있는 경우
-- 형식 : CONSTRAINT [제약명] FOREIGN KEY(참조컬럼) REFERENCES 참조할 테이블명(컬럼:PK)

-- [학사관리 시스템 설계]
-- 학생은 반드시 하나 이상의 과목을 수강해야 한다.
-- 교수는 반드시 하나 이상의 과목을 강의해야 한다.

-- 과목 테이블 생성 : SUBJECT
-- 컬럼 : 과목ID(SUB_ID), 과목명(SUB_NAME), 등록일자(SDATE)
-- 과목ID를 기본키로 설정
CREATE TABLE SUBJECT(
	SUB_ID	  CHAR(4),
    SUB_NAME  VARCHAR(30)	NOT NULL,
    SDATE	  DATE,
    CONSTRAINT SUBJECT_SUB_ID_PK PRIMARY KEY(SUB_ID)
);
SELECT * FROM INFORMATION_SCHEMA.TABLES WHERE TABLE_NAME = 'SUBJECT';
SELECT * FROM INFORMATION_SCHEMA.TABLE_CONSTRAINTS WHERE TABLE_NAME = 'SUBJECT';
DESC SUBJECT;

-- 학생 테이블 생성 : STUDENT
-- 컬럼 : 학번(SID:문자4), 학생명(SNAME), 학과(DEPT), 과목ID(SUB_ID), 이메일(EMAIL)
-- 학번은 기본키로 설정, 수강과목은 과목테이블을 참조한다.
CREATE TABLE STUDENT(
	SID		CHAR(4),
    SNAME	VARCHAR(20) NOT NULL,
    DEPT	VARCHAR(20),
    SUB_ID	CHAR(4),
    EMAIL	VARCHAR(20),
    CONSTRAINT STUDENT_SID_PK PRIMARY KEY(SID),
    CONSTRAINT STUDENT_SUB_ID_FK FOREIGN KEY(SUB_ID) REFERENCES SUBJECT(SUB_ID)
);
SELECT * FROM INFORMATION_SCHEMA.TABLES WHERE TABLE_NAME = 'STUDENT';
SELECT * FROM INFORMATION_SCHEMA.TABLE_CONSTRAINTS WHERE TABLE_NAME = 'STUDENT';
DESC STUDENT;

-- 교수 테이블 생성 : PROFESSOR
-- 컬럼 : 교수ID(PID), 교수명(PNAME), 폰번호(PHONE), 과목ID(SUB_ID)
CREATE TABLE PROFESSOR(
	PID		CHAR(4),
    PNAME	VARCHAR(20) NOT NULL,
    PHONE	VARCHAR(20),
    SUB_ID	CHAR(4),
    CONSTRAINT PROFESSOR_PID_PK PRIMARY KEY(PID),
    CONSTRAINT PROFESSOR_SUB_ID_FK FOREIGN KEY(SUB_ID) REFERENCES SUBJECT(SUB_ID)
);
DESC PROFESSOR;

-- 참조관계에서의 데이터 생성(추가)
-- 가장 데이터 추가되어져야 하는 테이블
DESC SUBJECT;
INSERT INTO SUBJECT(SUB_ID, SUB_NAME, SDATE) VALUES('S001', 'HTML', CURDATE());
INSERT INTO SUBJECT(SUB_ID, SUB_NAME, SDATE) VALUES('S002', 'MySQL', CURDATE());
INSERT INTO SUBJECT(SUB_ID, SUB_NAME, SDATE) VALUES('S003', 'Node', CURDATE());
INSERT INTO SUBJECT(SUB_ID, SUB_NAME, SDATE) VALUES('S004', 'React', CURDATE());
COMMIT;
SELECT * FROM SUBJECT;    

-- 학생 데이터 생성
DESC STUDENT;
INSERT INTO STUDENT VALUES('ST03', '홍길동', '컴퓨터공학', 'S001', 'hong@naver.com');
INSERT INTO STUDENT VALUES('ST04', '테스트', '컴퓨터공학', 'S003', 'test@naver.com');
INSERT INTO STUDENT VALUES('ST05', '홍길순', '경영학', 'S003', 'honghong@naver.com');
SELECT * FROM STUDENT;

-- 교수 데이터 생성
DESC PROFESSOR;
INSERT INTO PROFESSOR VALUES('P001', '공유', '010-1234-4587', 'S001');
INSERT INTO PROFESSOR VALUES('P002', '리사', '010-1234-4587', 'S002');
INSERT INTO PROFESSOR VALUES('P003', '제니', '010-1234-4587', 'S003');
-- INSERT INTO PROFESSOR VALUES('P004', '제니', '010-1234-4587', 'S009');
SELECT * FROM PROFESSOR;

-- 홍길동 학생이 수강하고 있는 과목의 교수명을 출력
SELECT ST.SNAME, SU.SUB_NAME, P.PNAME 
	FROM SUBJECT SU, STUDENT ST, PROFESSOR P
	WHERE SU.SUB_ID = ST.SUB_ID 
		AND SU.SUB_ID = P.SUB_ID
		AND ST.SNAME = '홍길동';
        
-- ANSI SQL

-- 테스트 학생이 수강하고 있는 과목을 모두 출력
SELECT SUB_NAME 
	FROM SUBJECT
    WHERE SUB_ID IN (SELECT SUB_ID FROM STUDENT WHERE SNAME ='테스트'); 
    -- 단일행 =,  다중행 IN
SELECT SUB_ID FROM STUDENT WHERE SNAME ='테스트';    

-- 테스트 학생이 수강하고 있는 과목, 학생명을 모두 출력
SELECT SU.SUB_NAME, ST.SNAME 
	FROM STUDENT ST, SUBJECT SU
    WHERE ST.SUB_ID = SU.SUB_ID
		AND ST.SNAME = '테스트';

-- 제니 교수가 강의하는 과목의 학생들을 모두 출력
SELECT ST.SID, ST.SNAME, ST.DEPT, ST.EMAIL
	FROM PROFESSOR P, SUBJECT SU, STUDENT ST
    WHERE P.SUB_ID = SU.SUB_ID
		AND SU.SUB_ID = ST.SUB_ID
        AND P.PNAME = '제니';

-- 과목별 수강인원, 과목명, 교수명 출력
SELECT SUB_NAME, 수강인원, PNAME
FROM (SELECT SU.SUB_NAME, COUNT(ST.SUB_ID) 수강인원, SU.SUB_ID
		FROM SUBJECT SU, STUDENT ST
		WHERE SU.SUB_ID = ST.SUB_ID
		GROUP BY ST.SUB_ID) SS LEFT OUTER JOIN PROFESSOR P
ON SS.SUB_ID = P.SUB_ID ;

--
SELECT SU.SUB_NAME, COUNT(ST.SUB_ID) 수강인원, P.PNAME
		FROM SUBJECT SU INNER JOIN STUDENT ST ON SU.SUB_ID = ST.SUB_ID
			LEFT OUTER JOIN PROFESSOR P ON SU.SUB_ID = P.SUB_ID
		GROUP BY SU.SUB_ID, P.PNAME;
		

SELECT * FROM PROFESSOR;
SELECT COUNT(*) FROM SUBJECT;  -- 4
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent16