
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent18 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/**
	자동 행번호 생성  : auto_increment
    형식 : 테이블 생성시 기본키 컬럼(중복X, NullX)에 데이터 전후에 입력
	- 기본키를 별도로 입력받지 않는 테이블에서 기본키의 역할을 하도록 정의
*/
-- 테이블 생성 : BOARD
SELECT * FROM INFORMATION_SCHEMA.TABLES WHERE TABLE_NAME LIKE 'BOARD%';
DROP TABLE BOARD;
CREATE TABLE BOARD(
	BID		INT		AUTO_INCREMENT primary KEY,
    BTITLE  VARCHAR(50)  NOT NULL,
    BCONTENT  VARCHAR(200),
    BDATE	DATE
);
SELECT last_insert_id() FROM BOARD;

INSERT INTO BOARD(BTITLE,BCONTENT,BDATE)
	VALUES('TEST','CONTENT TEST',CURDATE());
SELECT * FROM BOARD;
DELETE FROM BOARD WHERE BID=4;

-- BID를 문자열 + 숫자 : 'ABC00001' 
-- BOARD2 테이블 생성
DROP TABLE BOARD2;
CREATE TABLE BOARD2(
	BID		VARCHAR(10) primary KEY,
    BTITLE  VARCHAR(50)  NOT NULL,
    BCONTENT  VARCHAR(200),
    BDATE	DATE
);

DROP TABLE BNUMBER;
CREATE TABLE BNUMBER(
	ID  INT AUTO_INCREMENT PRIMARY KEY
);
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent18