import React, { useEffect, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faX } from "@fortawesome/free-solid-svg-icons";

interface PhotoModalProps {
  isModalOpen: boolean;
  onClose: () => void;
  photo: string;
}

const getWindowHeight = () => {
  return window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
};

const PhotoModal: React.FC<PhotoModalProps> = ({ photo, isModalOpen, onClose }) => {
  const modalStyle = {
    backgroundColor: isModalOpen ? "rgba(0, 0, 0, 0.8)" : "transparent",
    display: isModalOpen ? "block" : "none",
    zIndex: 400,
  };
  const [, setWinHeight] = useState(getWindowHeight);

  const handleCloseClick = () => {
    onClose();
  };

  const handleBackdropClick = (e: React.MouseEvent<HTMLDivElement>) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };


  useEffect(() => {
    const handleResize = () => {
      setWinHeight(getWindowHeight());
    };
    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, [photo]);

  return (
    <div
      className={`modal fixed left-0 top-0 w-full h-[97.5%] lg:h-[98.5%] rounded-2xl bg-black opacity-100 transition-opacity duration-100`}
      style={modalStyle}
      onClick={handleBackdropClick}
    >
      <div
        className={`modal-content flex flex-col items-center bg-transparent border-1 border-slate-300 overflow-hidden rounded-lg opacity-100 mx-auto]`}
      >
        <section className="p-[10px] h-[750px] md:h-[780px] lg:h-[1180px]">
          <button
            className={`closeBtn absolute right-[15px] top-[15px] w-[20px] h-[25px] ml-auto mb-[10px] transition-all duration-500 bg-white text-black`}
            onClick={handleCloseClick}
            style={{
              textShadow: "4px 4px 4px rgba(255, 255, 255, 1)",
            }}
          >
            <FontAwesomeIcon icon={faX} />
          </button>
          <div className={` upBox bg-transparent flex items-center justify-center  w-full h-full `}>
            <img className="w-[100%]" src={photo} alt="" />
          </div>
        </section>
      </div>
    </div>
  );
};

export default PhotoModal;
