
import { useEffect, useState } from 'react';
import FrontendCookify from './frontend/frontendProjects/FrontendCookify';
import FrontendCoinView from './frontend/frontendProjects/FrontendCoinView';
import FrontendMyBlog from './frontend/frontendProjects/FrontendMyBlog';
import FrontendMovie from './frontend/frontendProjects/FrontendMovie';
import FrontendWebbler from './frontend/frontendProjects/FrontendWebbler';
import FrontendBasic from './frontend/frontendProjects/FrontendHowToUse';
import { useDarkMode } from '../../context/Dark-mode';
import { useAllContext } from '../../context/AllContext';
import BackendCoinView from './backend/backendProjects/BackendCoinView';
import BackendCreateBasicSite from './backend/backendProjects/BackendCreateBasicSite';
import BackendDwitter from './backend/backendProjects/BackendDwitter';
import BackendAmuWiki from './backend/backendProjects/BackendAmuwiki'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCaretLeft, faCaretRight } from '@fortawesome/free-solid-svg-icons';

export const components = [
  <FrontendCookify />,
  <FrontendCoinView />,
  <BackendCoinView />,
  <FrontendMyBlog/>,
  <BackendAmuWiki />,
  <FrontendMovie />,
  <FrontendWebbler />,
  <FrontendBasic />,
  <BackendDwitter />,
  <BackendCreateBasicSite/>
];

export const componentsNameIndex = [
  'Cookify Project (FE)',
  'CoinView Project (FE)',
  'CoinView Project (BE)',
  'My Blog Project (FE)',
  'AmuWiki Project (BE)',
  'Movie Project (FE)',
  'Webbler Project (FE)',
  'Basic Project (FE)',
  'Dwitter Project (BE)',
  'Create Basic Site Project (BE)',
]

const AllContainer = () => {
  const {darkMode} = useDarkMode()
  const [parentWidth, setParentWidth] = useState(0);

  const { handleSwipeLeft, handleSwipeRight, currentIndex } = useAllContext();

  const isAtFirstPage = currentIndex === 0;
  const isAtLastPage = currentIndex === components.length - 1;



  useEffect(() => {
    const handleResize = () => {
      const parentElement = document.getElementById('parent');
      if (parentElement) {
        setParentWidth(parentElement.offsetWidth); 
      }
    };
    handleResize();

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const width = parentWidth / components.length; 

  return (
    <div className='w-full h-[600px] lg:h-[1000px] my-auto p-[5px] pt-0 md:p-[20px] md:pt-0'>
      <div id="parent" className='w-[100%] my-[1px] h-[15px] md:h-[18px] rounded-md bg-slate-300  top-0'>
        <div style={{ width: `${width}px`, transform : `translateX(${width * currentIndex - 1}px)` }} className={`flex flex-col items-center justify-center h-[100%] transition-all  rounded-md border-[2px] ${darkMode ? 'bg-blue-500 border-slate-300' : 'bg-[#86756b] border-slate-300'} `}>
          <span className={`font-bold text-[15px] ${darkMode ? 'text-white' : 'text-white'}`}>{currentIndex + 1}</span>
        </div>
      </div>
      <div className={` h-full flex flex-col items-center justify-center relative ${darkMode ? 'bg-slate-900 text-white' : 'bg-white text-black'}`}>
        <div
          className={`absolute w-[100%] md:w-[80%] translate-y-[-246px] md:translate-y-[-234px] lg:translate-y-[-434px] flex flex-row items-center justify-center py-[20px] pt-0 ${darkMode ? 'border-slate-500 bg-[#0e172a]' : 'border-slate-300 bg-white'}`}
          style={{zIndex : 200}}
        >

          <div>
            {
              isAtFirstPage ?
                (
                  <button className='block translate-x-[-20px] opacity-30 '>
                    <FontAwesomeIcon icon={faCaretLeft} className='md:ml-[20px] text-3xl hover:scale-150 transition-all' />
                  </button>
                )
                  :
                (
                  <button className='block translate-x-[-20px] ' onClick={handleSwipeLeft}>
                    <FontAwesomeIcon icon={faCaretLeft} className='md:ml-[20px] text-3xl hover:scale-150 transition-all' />
                  </button>
                ) 
            }
          </div>
          <h2 className={`text-[13px] md:text-xl lg:text-2xl text-center w-[200px] sm:w-[300px] lg:w-[400px] font-bold ${darkMode ? 'text-white' : 'text-black'}`}>{componentsNameIndex[currentIndex]}</h2>
          <div>
            {
              isAtLastPage ?
                (
                  <button className='block translate-x-[20px] opacity-30'>
                    <FontAwesomeIcon icon={faCaretRight} className='md:mr-[20px] text-3xl hover:scale-150 transition-all' />
                  </button>
                )
                  :
                (
                  <button className='block translate-x-[20px]' onClick={handleSwipeRight}>
                    <FontAwesomeIcon icon={faCaretRight} className='md:mr-[20px] text-3xl hover:scale-150 transition-all' />
                  </button>
                )
            }
          </div>
        </div>
        {components[currentIndex]}
      </div>
    </div>
  );
};

export default AllContainer;