import { useState } from 'react';
import { useDarkMode } from '../../../../../context/Dark-mode';
import { ElasticSearchbutton, MongoDBbutton, MySQLbutton, PostgreSQLbutton } from '../../../../../icons/StackButtons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleDown, faAngleUp } from '@fortawesome/free-solid-svg-icons';
import MysqlComponent from './mysql/mysql';
import PostgreSQLComponent from './postgresql/postgresql';
import MongoDBComponent from './mongodb/mogodb';
import ElasticSearchComponent from './elasticsearch/elasticsearch';

const DataBaseDocs = () => {

  const {darkMode} = useDarkMode()

  const [selectedCategory, setSelectedCategory] = useState<string | null>('mysql');
  const [renderedContent, setRenderedContent] = useState<React.ReactNode | null>(<MysqlComponent/>);
  const [isCategoryVisible, setIsCategroyVisible] = useState(true)

  const handleCategoryClick = (category: string) => {
    setSelectedCategory(category);
    setIsCategroyVisible(false)

    if (category === 'mysql') {
      setRenderedContent(<MysqlComponent />);
    } else if (category === 'postgresql') {
      setRenderedContent(<PostgreSQLComponent />);
    } else if (category === 'mongodb') {
      setRenderedContent(<MongoDBComponent />);
    } else if (category === 'elasticsearch') {
      setRenderedContent(<ElasticSearchComponent />);
    }
  };

  const clickDownBtn = () => {
    setIsCategroyVisible(true)
  }

  const clickUpBtn = () => {
    setIsCategroyVisible(false)
  }



  return (
    <div className='outBox w-full h-[95%] px-[5px] md:py-0 md:px-[20px]'>
      <div
        className={`w-[100%] relative h-[100%] flex flex-col items-center rounded-md text-center ${darkMode ? 'bg-slate-700' : 'bg-[#e8e6e4]'}`}>
        <section
          className={`fixed  flex flex-col justify-between w-[100%] px-[30px]  ${isCategoryVisible ? 'h-[95%] md:h-[200px]' : ''} ${darkMode ? 'bg-[#64748b]' : 'bg-[#c7c1bb]'} `}
          style={{ zIndex : 10 }}
        >
          <div className={`flex flex-col flex-wrap py-[20px] md:flex-row gap-2 items-center overflow-y-auto justify-center ${isCategoryVisible ? '' : 'hidden'}`}>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'mysql' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'}` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('mysql')}
            > 
              <MySQLbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px]  md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'postgresql' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('postgresql')}
            >
              <PostgreSQLbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'mongodb' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('mongodb')}
            >
              <MongoDBbutton/>
            </button>
            <button
              className={`p-[5px] w-full md:w-[130px] h-[50px] md:h-[35px] rounded-md transition-all ${
                selectedCategory === 'elasticsearch' ? `  text-white scale-97 ${darkMode ? 'bg-slate-300':'bg-[#86756b]'} ` : ` ${darkMode ? 'text-white ' : 'text-black '} `
              }`}
              onClick={() => handleCategoryClick('elasticsearch')}
            >
              <ElasticSearchbutton/>
            </button>
          </div>
          <div className=''>
            <button
              className={`w-full ${isCategoryVisible ? '' : 'hidden'}`}
              onClick={clickUpBtn}
            >
                <FontAwesomeIcon icon={faAngleUp} />
              </button>
            <button
              className={`w-full flex flex-col items-center ${isCategoryVisible ? 'hidden' : ''}`}
              onClick={clickDownBtn}
            >
                <span className='my-[10px]'>Category</span>
                <FontAwesomeIcon icon={faAngleDown} />
              </button>
          </div>
        </section>
        <section className='w-full h-full mt-[60px]'>
          {renderedContent}
        </section>
      </div>
    </div>
  )
}

export default DataBaseDocs