
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent15 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/*
	DDL : Database Definition Language
    - 데이터베이스의 테이블 구조 생성, 삭제, 변경
    테이블 생성 : CREATE TABLE
    테이블 삭제 : DROP TABLE
    테이블 변경 : ALTER TABLE
    
    DML : Database Manuplation Lauguage
    - 데이터 생성, 변경, 삭제
    데이터 생성 : INSERT INTO 
    데이터 변경 : UPDATE
    데이터 삭제 : DELETE
    데이터 조회 : SELECT
 */
 
 -- 테이블 목록 조회 : INFORMATION_SCHEMA.TABLES
 DESC INFORMATION_SCHEMA.TABLES;
 
 -- HRDB2019 데이터베이스의 모든 테이블 조회
 SELECT * FROM INFORMATION_SCHEMA.TABLES  -- 오라클 : USER_TABLES 데이터 딕셔너리 테이블
 WHERE TABLE_SCHEMA = 'HRDB2019';
 
 -- 테이블 생성
 -- 형식 : CREATE TABLE [테이블명] (
 --   	     컬럼명 	데이터타입(크기)	[제약사항],
 -- 		...  )
DESC EMPLOYEE;		
-- EMP 테이블 생성 : EMP_ID(문자,10), EMP_NAME(문자,20), HIRE_DATE(DATE), SALARY(숫자) 
CREATE TABLE EMP(
	EMP_ID	CHAR(10),  -- 고정형 문자데이터 타입
    EMP_NAME VARCHAR(20),  -- 가변형 문자데이터 타입
    HIRE_DATE DATE,
    SALARY INT
); 
SELECT * FROM INFORMATION_SCHEMA.TABLES
WHERE TABLE_SCHEMA = 'HRDB2019';
DESC EMP;

-- 테이블 생성(복제)
-- 형식 : CREATE TABLE [테이블명]
-- 		 AS
-- 		 SELECT ~~
--  **복제한 테이블에는 원본의 제약사항은 포함되지 않는다

-- EMPLOYEE 테이블을 복제하여 EMP2 테이블을 생성
SELECT * FROM INFORMATION_SCHEMA.TABLES WHERE TABLE_SCHEMA = 'HRDB2019';

-- EMPLOYEE 테이블의 구조만 복제
CREATE TABLE EMPLOYEE_COPY
AS
SELECT * FROM EMPLOYEE WHERE 1=0;

DESC EMPLOYEE_COPY;  
DESC EMPLOYEE;
SELECT * FROM EMPLOYEE_COPY;

-- 정보시스템 부서의 사원들만 EMPLOYEE_SYS 테이블에 저장
CREATE TABLE EMPLOYEE_SYS
AS
SELECT * FROM EMPLOYEE 
WHERE DEPT_ID = (SELECT DEPT_ID FROM DEPARTMENT WHERE DEPT_NAME = '정보시스템');

SELECT * FROM EMPLOYEE_SYS;

-- 테이블 삭제
-- DROP TABLE [테이블명];
DROP TABLE EMP;

SELECT * FROM INFORMATION_SCHEMA.TABLES WHERE TABLE_SCHEMA ='HRDB2019';
DESC EMP;
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent15